<template>
    <div class="discount">
    	<div class="discount_banner">
    		 <swiper :options="swiperOption">
		        <swiper-slide><img src="../../assets/dis_banner1.png"/></swiper-slide>
		        <swiper-slide><img src="../../assets/dis_banner2.png"/></swiper-slide>
		        <swiper-slide><img src="../../assets/dis_banner3.png"/></swiper-slide>
		        <swiper-slide><img src="../../assets/dis_banner4.png"/></swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    	</div>
    	<div class="justAword" v-if="false">
    		无论何地都有都会可领<p>找优惠</p>
    	</div>
    	<div class="scrollBox" @click="clickAll">
    	<div class="discountCoupon">
    		<div class="top">
    			<div>优惠券</div>
    			<div class="moreCoupon" @click="to_moreCoupon"> 更多  ＞ </div>
    		</div>
    		<div class="goods">
    			  <swiper :options="swiperOption2" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in coupon" :key="data.id">
			        		<div class="goodsLogo" @click="youhui(data)"><img :src="'http://'+data.goodLogo" alt=""></div>
			        		<div class="goodsRight">
			        			<div class="goodsName">{{data.goodName}}</div>
			        			<div class="goodsFeature">{{data.goodTrait}}</div>
			        			<div class="rightBottom">
			        				<div class="merchantLogo"><img :src="'http://'+data.platformLogo" alt=""></div>
			        				<div class="merchantName">{{data.shopName}}</div>
			        				<div class="people">{{data.buyNumber}}人领取</div>
			        			</div>
			        		</div>
			        </swiper-slide>
			      </swiper>
    		</div>
    	</div>
    	<div class="discountCoupon">
    		<div class="top">
    			<div>免费课程</div>
    			<div class="moreCoupon" @click="to_freeLesson"> 更多  ＞ </div>
    		</div>
    		<div class="goods">
    			  <swiper :options="swiperOption2" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in freeClasses" :key="data.id">
			        		<div class="goodsLogo" @click="free(data)"><img :src="'http://'+data.goodLogo" alt=""></div>
			        		<div class="goodsRight">
			        			<div class="goodsName">{{data.goodName}}</div>
			        			<div class="goodsFeature">{{data.goodTrait}}</div>
			        			<div class="rightBottom">
			        				<div class="merchantLogo"><img :src="'http://'+data.platformLogo" alt=""></div>
			        				<div class="merchantName">{{data.shopName}}</div>
			        				<div class="people">{{data.buyNumber}}人领取</div>
			        			</div>
			        		</div>
			        </swiper-slide>
			      </swiper>
    		</div>
    	</div>
    	<!--<div class="discountCoupon">
    		<div class="top">
    			<div>0元购</div>
    			<div class="moreCoupon" @click="to_zeroBuy"> 更多  ＞ </div>
    		</div>
    		<div class="goods">
    			  <swiper :options="swiperOption2" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in couponList" :key="data.id">
			        		<div class="goodsLogo" @click="Oyuan(data)"><img src="../../assets/0yuan.jpg" alt=""></div>
			        		<div class="goodsRight">
			        			<div class="goodsName">{{data.name}}</div>
			        			<div class="goodsFeature">{{data.feature}}</div>
			        			<div class="rightBottom">
			        				<div class="merchantLogo">{{data.merchantLogo}}</div>
			        				<div class="merchantName">{{data.merchantName}}</div>
			        				<div class="people">{{data.people}}人领取</div>
			        			</div>
			        		</div>
			        </swiper-slide>
			      </swiper>
    		</div>
    	</div>-->
    	<div class="discountCouponShop">
    		<div class="top">
    			<div>优惠商城</div>
    			<div class="moreCoupon" @click="to_DiscountMall"> 更多  ＞ </div>
    		</div>
    		<div class="shopgoods">
	    		<swiper :options="swiperOption3" class="swiperbox">
				  <swiper-slide class="carts" v-for="data in shopDatalist" :key="data.id">
				  		<div class="content_shop">
			                    <div class="style" @click="shop(data)"><img :src="'http://'+data.goodLogo" alt=""></div>
			                    <div class="name">{{data.goodName}}</div>
			                    <div class="price"><span>到手价￥</span>{{data.theirPrice}}</div>
			                    <div class="old_price">原价￥{{data.primeCost}}</div>
			                    <div class="btm">
		                        <div class="logo"><img :src="'http://'+data.platformLogo" alt=""></div>
		                        <div class="merchant">{{data.shopName}}</div>
		                        <div class="num">{{data.buyNumber}}人购买</div>
		                    </div>
		                </div>
					</swiper-slide>
				</swiper>
			</div>
    	</div>
    	</div>
    </div>
</template>

<script>
	import 'swiper/dist/css/swiper.css'
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
    	data(){
    		return{
    			   swiperOption: {
    			   			autoplay:{
    			   				autoplay:true
    			   			},
				          pagination: {
				            el: '.swiper-pagination',
				            dynamicBullets: true
				          }
				       },
				       swiperOption2:{				       	
				          slidesPerView: 1.3,
				          spaceBetween: 0,
				          freeMode: true
				       },
				       //优惠商城的配置， 真的是妖艳
				       swiperOption3:{
				       	slidesPerView: 1.6,
				          spaceBetween: 10,
				          freeMode: true
				       },
				datalist:[{id:1},{id:2},{id:3},{id:4}],
					//优惠商城
					shopDatalist:[],
					//优惠券
					coupon:[],
					//免费课程
					freeClasses:[]
    		}
    	},
    	methods:{
    		to_moreCoupon(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/ticket')
    			}		
    		},
    		to_freeLesson(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/freeclass')
    			}		
    		},
    		to_zeroBuy(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/buy')
    			}		
    		},
    		to_DiscountMall(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/shop')
    			}		

    		},
    		clickAll(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}
    		},
			//跳第三方
			youhui(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    			this.$router.push({
                    path:'/third',
     
    	 query:{
                        title:"优惠券",
                        url:data.goodLink
                    }
                })
    			}
                
			},
			free(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    				this.$router.push({
                    path:'/third',
                    query:{
                        title:"免费课程",
                        url:data.goodLink
                    }
                })
    			}
				
			},
			Oyuan(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    				this.$router.push({
                    path:'/third',
                    query:{
                        title:"0元购",
                        url:data.goodLink
                    }
                })
    			}
                
			},
			shop(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    				this.$router.push({
                    path:'/third',
                    query:{
                        title:"优惠商城",
                        url:data.goodLink
                    }
                })
    			}
				
			}
   },
   mounted(){
	   //优惠商城
	   	this.$http({
						method:'POST',
						url:this.$discountMallSearch,
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":5,
						 	 "page.currentPage":1
						 }
				}).then((res)=>{
						this.shopDatalist = res.data.data
					})
		//优惠券
		this.$http({
						method:'POST',
						url:this.$couponSearch,
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":5,
						 	 "page.currentPage":1
						 }
				}).then((res)=>{
						console.log(res.data.data)
						this.coupon = res.data.data
					})
		//免费商城 
		this.$http({
						method:'POST',
						url:this.$freeClassesSearch,
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":5,
						 	 "page.currentPage":1
						 }
				}).then((res)=>{
						console.log(res.data.data)
						this.freeClasses = res.data.data
						console.log(this.freeClasses)
					})
   },
   
      components:{swiper,swiperSlide},
    }
</script>

<style lang="scss" scoped>
   .discount{
   	width: 100%;
   	height: 100%;
   	overflow: hidden;
   	background: #ffffff;
   	display: flex;
   	flex-direction: column;
	.discount_banner{
		width: 100%;
		height: 7.5rem;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.justAword{
		width: 100%;
		height: 5.75rem;
		padding-top: 2rem;
		text-align: center;
		font-size: 0.75rem;
		color: #000000;
		background: #fae44c;
		p{
			margin-top: 0.31rem;
		}
	}
	.scrollBox{
		width: 102%;
		flex: 1;
		padding-right: 2%;
		overflow-x: hidden;
   		overflow-y: scroll;
   		margin-top: .5125rem;
	}
	.discountCoupon{
		width: 100%;
		height: 8.03rem;		
		.top{
			width: 100%;
			height: 1.75rem;
			background: #ffffff;
			display: flex;	
			padding: 0 0.62rem;
			justify-content: space-between;
			font-size: 1.09rem;
			color: #292534;
			align-items: center;
			
			.moreCoupon{
				font-size: 0.84rem;
				color: #8c8c8c;
			}
			}
			.goods{
				width: 100%;
				height: 6.25rem;
				padding-left: 0.62rem;
				padding-top: 0.93rem;
				background: #ffffff;
				border-bottom: 1px solid #efefef;
				.swiperbox{
					width: 100%;
					height: 4.46rem;
					.carts{
						width: 16.65rem;
						height: 100%;
						display: flex;
						.goodsLogo{
							width: 4.46rem;
							height: 4.46rem;
							text-align: center;
							line-height: 4.46rem;
							img{height: 100%;width: 100%;display: inline-block;}
						}
						.goodsRight{
						flex: 1;
						height: 100%;
						padding-left: 0.62rem;
						.goodsName{
							width: 9.68rem;
							height: 2.18rem;
							color: #4a4a4a;
							font-size: 0.96rem;
							line-height: 0.93rem;
							padding-top: 0.31rem;
							overflow:hidden; 
							text-overflow:ellipsis;
							display:-webkit-box; 
							-webkit-box-orient:vertical;
							-webkit-line-clamp:2; 
						}
						.goodsFeature{
						
							height: 0.87rem;
							/*background: #fcf4df;
							border: 1px solid #fd520c;
							border-radius: 3px;*/
							color: #e95120;
							font-size: 0.87rem;
							line-height: 0.87rem;
							margin-top: 0.15rem;
							margin-bottom: 0.25rem;
						}
						.rightBottom{
							width: 100%;
							height: 1.06rem;
							display: flex;
							align-items: center;
							color: #000000;
							font-size: 0.62rem;
							.merchantLogo{
								width: 0.78rem;
								height: 0.78rem;
								border: 1px solid #bfbfbf;
								img{
									width: 100%;
									height: 100%;
								}
							}
							.merchantName{
								margin-left: 0.62rem;	
								color: #6f6e6e;	
								font-size: 0.75rem;						
							}
							.people{
								color: #6f6e6e;
								font-size: 0.75rem;
								margin-left:0.43rem;
							}
						}
						}
					}
				}
			}
	}
	.discountCouponShop{
		width: 100%;
		.top{
			width: 100%;
			height: 1.75rem;
			background: #ffffff;
			display: flex;	
			padding: 0 0.62rem;
			justify-content: space-between;
			font-size: 1.09rem;
			color: #292534;
			align-items: center;
			.moreCoupon{
				font-size: 0.84rem;
				color: #8c8c8c;
			}
			}
			.shopgoods{				
          		 width: 100%;
				height: 19.25rem;
				padding-left: 0.62rem;
				padding-top: 0.93rem;
				background: #ffffff;
				.swiperbox{
					width: 100%;
					height:100%;
					.carts{
						
						.content_shop{
                    width: 80%;height: 100%;
                    .style{height: 10.59rem;width: 100%;overflow: hidden;border-radius: 5px;
                    	img{height: 100%;width: 100%;display: inline-block;}
                    }
                    .name{width: 100%;height: 2.84rem;line-height: 2.84rem;overflow: hidden;
                            text-overflow: ellipsis;white-space: nowrap;color: #010101;font-size: 1.03rem;}
                    .price{height: 1.15rem;width:100%;line-height: 1.15rem;color: #fb0200;font-size: 1.15rem;
                    		span{
                    			font-size: 0.93rem;
                    		}
                    }
                    .old_price{height: 1.71875rem;line-height: 1.71875rem;color: #9f9f9f;font-size: .6875rem;text-decoration: line-through;}
                    .btm{
                        width: 100%;height: 1.0625rem;line-height: 1.0625rem;font-size: .625rem;
                        .logo{height: 1.03125rem;width: 1.03125rem;float: left;border: 1px solid #eeeeee;
								img{height: 100%;width: 100%;}
								}
                        .merchant{float: left;margin-left: .3125rem;color: #6f6e6e;font-size: 0.75rem}
                        .num{float: right;color: #6f6e6e;font-size: 0.75rem}
                        }
                		}
					}
				}
          
        
			}
	}
   }
</style>



