<template>
    <div class="buy_class">
        <div class="header">
            <div class="back" @click="buyBack"></div>
			<div class="center">优惠商城</div>
            <div class="block"></div>
        </div>

        <div class="bottom">
		  <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">			
            <div class="bigbox">
	            <div class="box" v-for="data in datalist" :key="data.id" @click="goDetail(data)">
	                <div class="content_shop">
	                    <div class="style"><img :src="'http://'+data.goodLogo" alt="" style="width: 100%;height: 100%;"></div>
	                    <div class="name">{{data.goodName}}</div>
	                    <div class="price"><span>到手价￥</span>{{data.theirPrice}}</div>
	                    <div class="old_price">原价￥{{data.primeCost}}</div>
	                    <div class="btm">
	                        <div class="logo"><img :src="'http://'+data.platformLogo" alt="" style="width: 100%;height: 100%;"></div>
	                        <div class="merchant">{{data.shopName}}</div>
	                        <div class="num">{{data.buyNumber}}人购买</div>
	                    </div>
	                </div>
	            </div>
            </div>
		<div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../assets/cry.png"/></div>
		</v-loadmore>	
			
        </div>
        
    </div>
</template>

<script>
	import { Loadmore } from 'mint-ui';
    export default {
    	components:{
    	  'v-loadmore':Loadmore,
  		},
        name:"FreeClass",
        data(){
            return{
                datalist:[],
                pageNo:1,
                pageSize:10,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false
            }
        },
        methods: {
            buyBack(){
                this.$router.push('/discount')
            },
            //点击转第三方
            goDetail(data){
                this.$router.push({
                    path:'/third',
                    query:{
                        title:"优惠商城",
                        url:data.goodLink
                    }
                })
            },
            loadBottom:function() {
		      // 上拉加载
		      event.preventDefault();
			  event.stopPropagation();
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
				this.$http({
						method:'POST',
						url:this.$discountMallSearch,
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":this.pageSize,
						 	 "page.currentPage":this.pageNo
						 }
				}).then((res)=>{
                    console.log(res)
						if(res.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;							
						}else{
							this.datalist = this.datalist.concat(res.data.data)
						}
						
					})
			
			},
			more(){
			   	this.pageNo++
			   	this.loadList(true);
		   }
        },
        mounted(){
			this.loadList(false)
        	
        }
    }
</script>

<style lang="scss">
    .buy_class{
         	width: 100%;
            height: 100%;
            background: #ffffff;      
            display: flex;
            flex-direction: column;
       
        .header{
            width: 100%;
	        height: 2.85rem;
            background: #fae44c;
            padding-left: 0.62rem;
            padding-right: 0.62rem;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../assets/back.png) no-repeat center center;
            background-size: 0.62rem 1.28rem;
                }
                .center{
            width: 7.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #333333;
            }
            .block{
                width: 2.62rem;
            }
 	     }

        .bottom{
            height: 100%;
            width: 100%;
            overflow: auto;
            background: #ffffff;
            padding:0 .625rem;
            padding-bottom:.625rem;
            .mint-loadmore-content{
            	 height: 100%;
           		 width: 100%;
            	.bigbox{
            		 height: 100%;
           		 width: 100%;
            	display: flex;
	            justify-content: space-between;
	            flex-wrap: wrap;
            		
            		.box{
                width: 50%;height: 19.12rem;
                background: #ffffff;
                border-bottom: 1px solid #eeeeee;
                
                .content_shop{
                    width: 100%;height: 100%;padding-top:1rem;
                    .style{height: 10.59rem;width: 100%;overflow: hidden;border-radius: 5px;}
                    .name{width: 100%;height: 2.84rem;line-height: 2.84rem;overflow: hidden;
                            text-overflow: ellipsis;white-space: nowrap;color: #010101;font-size: 1.03rem;}
                    .price{height: 1.15rem;width:100%;line-height: 1.15rem;color: #fb0200;font-size: 1.15rem;
                    		span{
                    			font-size: 0.93rem;
                    		}
                    }
                    .old_price{height: 1.71875rem;line-height: 1.71875rem;color: #9f9f9f;font-size: .6875rem;text-decoration: line-through;}
                    .btm{
                        width: 100%;height: 1.0625rem;line-height: 1.0625rem;font-size: .625rem;
                        .logo{height: 1.03125rem;width: 1.03125rem;float: left;}
                        .merchant{float: left;margin-left: .3125rem;color: #6f6e6e;font-size: 0.75rem;}
                        .num{float: right;color: #6f6e6e;font-size: 0.75rem;}
                        }
                }
            }
            .box:nth-child(2n+1){
				border-right: 1px solid #eeeeee;
				background: #fffffff;
				padding-right: 0.43rem;
            }
            .box:nth-child(2n){
				padding-left: 0.43rem;
            }
            	}
            	
            	
            }
            
           
        }

    }
</style>


