<template>
		<div class="index">
			<!--引导页-->
				<Yindao class="yindao" v-if="isTrue" @to-parent="getChild" ></Yindao>	
				
				<!--首页路由区域-->
				<div class="main" v-if="isTrue1">
					<router-view></router-view>
				</div>
				<!--首页footer-->
					<div class="footer" v-if="isTrue1">
						
						
						<router-link tag="div" :to="'/makemoney'">
							<i class="iconfont icon-qian"></i>
							<span>去赚钱</span>
						</router-link>
						<router-link tag="div" :to="'/discount'">
							<i class="iconfont icon-youhuiquan"></i>
							<span>找优惠</span>
						</router-link>
						<!--<router-link tag="div" :to="'/GoTeam'">
							<i class="iconfont icon-tuandui"></i>
							<span>组团队</span>
						</router-link>-->
						<router-link tag="div" :to="'/my'">
							<i class="iconfont icon-xiazai"></i>
							<span>我的</span>
						</router-link>

					</div>
				
		</div>
		 
		 
</template>

<script>
import Yindao from "@/components/YinDao"
import '@/assets/iconfont/iconfont.css'
export default {
  name: 'index',
  components:{ Yindao },
  data () {
    return {
      isTrue:true,
      isTrue1:false,
      isContent:false,
      title:"产品名称"
    }
  },
  methods:{
  	getChild(){
		this.isTrue=false;
		this.isTrue1 =true
		this.isContent = true;
		//this.$router.push('/login')
  		
  	}
 
  },
  created(){
  	//读取localstorage 如果不是第一次打开app 不显示引导页
      var huancun1 = localStorage.getItem("yindao");
      if (huancun1 == "cun") {
      	  this.isTrue=false;
      	  this.isTrue1 = true
      }else{
      	this.isTrue = true;
      	this.isTrue1 = false
      }
  },
//mounted(){
//	this.$http({
//     			method:"GET",
//     			url:'http://m.playmy.cn/View/Wall_AdList.aspx'
////     			params:{
////     				phone:this.phone
////     			}
//     		}).then((res)=>{
//     			console.log(res)
//     		})
//}
	   

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.yindao{
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 999;
	}

	.index{
		position: relative;
		width: 100%;
		height: 100%;		
		.main{
			width: 100%;
			height: 100%;
			padding-bottom: 3.06rem;
			overflow: hidden;
		}
		.footer{
			border-top: 1px solid #f2f2f2;
			z-index: 999;
			position: fixed;
			bottom: 0;
			background: #ffffff;
			height: 3.06rem;
			width: 100%;
			padding: 0 1.78rem;
			padding-top: 0.59rem;
			padding-bottom: 0.2rem;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.router-link-active{
			  color: #f9714b;
			}
			div{
				display: flex;
				height: 100%;
				align-items: center;
				justify-content: space-between;
				flex-direction: column;
				color: #5c5f66;
				.iconfont{
					  font-family:"iconfont" !important;
					  font-size:1.34rem;font-style:normal;
					  -webkit-font-smoothing: antialiased;
					  -webkit-text-stroke-width: 0.2px;
					  -moz-osx-font-smoothing: grayscale;
					}
					span{
						font-size: 0.56rem;
					}
			}
		}
	}

	
	
	
</style>



