<template>
	<!--办卡任务预留信息-->
	<div class="reservedInformation">		
		<div class="information_header">
			<div class="back" @click="back"></div>
			<div class="center">预留信息</div>
			<div class="block"></div>
		</div>
		<div class="warn">
		<img src="../../../../assets/laba.png" alt="">	未满18周岁或成功申办过此信用卡，将无法获得奖励
		</div>
		<!--横向展示-->
		<div class="crosswise">
		      <!-- swiper -->
			      <swiper :options="swiperOption" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in datalist" :key="data.id">
			        	<div class="cart_top">{{data.data.taskName}}<span>卡片类型{{data.data.taskType}}</span></div>
							<div class="cart_center">
								<div class="logo"><img :src="'http://'+data.data.taskLogo" alt=""></div>
								<div class="center_right">
									<div class="price">{{data.data.reward}}元</div>
									<div class="people">可申请人数{{data.data.taskNumber}}人</div>
								</div>
							</div>
							<div class="cart_bottom">
								<span class="feature1">{{data.data.taskDescription}}</span> 
							</div>
			        	
			        </swiper-slide>
			      </swiper>
		</div>
		<!--表单提交-->
		<div class="apply_submit">
			<div class="big_box">
				<div class="top">申请无需费用，不激活卡片不收年费</div>
				<div class="buttom">
					<div class="name">
						<span>姓名</span>
						<input type="text" v-model="applyName"/>
					</div>
					<div class="phone_number">
						<span>手机号</span>
						<input type="text" v-model="applyNumber"/>
					</div>
					<div class="apply_submit_button" @click="applyButton">免费申请</div>
				</div>
			</div>
		</div>
		<div class="zheZhao" v-show="zheZhao">
			<div class="sureBox" v-if="showWho=='sureBox'">
				<p class="sureP">请确认使用</p>
				<div class="applyNumber">{{this.applyNumber}}</div>
				<p class="sureP2">手机号申请，保证申请资料全面真实，通过率则会高</p>
				<div class="toBank" @click="to_bank">点击前往银行官网申请</div>
				<div class="chacha" @click="chacha"></div>
			</div>
		</div>
		<div class="err_num" v-show="err_isshow">{{err_content}}</div>
	</div>
</template>

<script>
	import 'swiper/dist/css/swiper.css'
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	export default{
		data(){
			return{
				 swiperOption: {
//				grabCursor: true,
//				centeredSlides: true,
//				slidesPerView: 'auto',  
//				coverflowEffect: {
//					rotate: 20,
//					stretch: -10,
//					depth: 100,
//					modifier: 1,
//					slideShadows : true
//					}
     
      		  },
      		  toUrl:"",
        //输入框姓名
        applyName:'',
        phone:"",
        goodsId:"",
        userId:"",
        //输入框手机号码
        applyNumber:"",
        //弹窗遮罩
        zheZhao:false,
		// 显示哪个弹窗     
        showWho:'sureBox',
        //错误信息弹窗
        err_isshow:false,
        err_content:'',
				datalist:[
                    
                    ]
			}
		},
		components:{swiper,swiperSlide},
		methods:{
			back(){
				this.$router.go(-1)
			},
			applyButton(){
			let myreg=/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/; 
			let regAliName = /^([\u4e00-\u9fa5]){2,7}$/;
			
				if(this.applyName =='' || this.applyNumber == ''){
					this.err_isshow = true;
					var that = this;
						this.err_content="请输入完整信息";
						clearInterval(timer)
						var timer = setInterval(function(){
							that.err_isshow =false;
						},3000)
						clearInterval();					
				}
					//判断是否输入正确手机号
					else if(!myreg.test(this.applyNumber)) {
						this.applyNumber = this.applyNumber.slice(0,11)
						this.err_isshow = true;
						var that = this;
						this.err_content="请输入正确的手机号码";
						clearInterval(timer)
						var timer = setInterval(function(){
							that.err_isshow =false;
						},3000)
						clearInterval();
						return false;
					}else if(!regAliName.test(this.applyName)){
						this.err_isshow = true;
						var that = this;
						this.err_content="请输入正确姓名";
						clearInterval(timer)
						var timer = setInterval(function(){
							that.err_isshow =false;
						},3000)
						clearInterval();
						return false;
					}
				else{
					this.zheZhao= true
				}
				
			},
			chacha(){
				this.zheZhao = false
			},
			//前往银行，预留信息
			to_bank(){
				this.$http({
					method:"POST",
					url:this.$partakeTask,
					params:{
						userId:this.userId,
						taskId:this.goodsId,
						phone:this.phone,
						name:this.applyName
					},
					headers: {
							"Content-Type" : "application/json"
						  }
					
				}).then((res)=>{
					console.log(res)
					if(res.data.resultCode==0){
						this.$router.push({path:'/third',query:{title:'办卡',url:this.toUrl}})		
						//window.location.href = this.toUrl
					}
				})
			}
		},
		created(){
		
			},
		mounted(){
			let goodsId = this.$route.params.id
			this.goodsId = goodsId
			   this.$http({
						method:'get',
						url:this.$getTaskDetail+"/"+goodsId,
						  headers: {
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						console.log(response.data.data.skipLink)
						this.datalist = [response.data]
						this.toUrl = response.data.data.skipLink
					}).catch((err)=>{
						console.log(err)
					})	
		},
		beforeMount(){
			 let user = localStorage.getItem("userId")
             this.phone = JSON.parse(user).phone            
             this.userId = JSON.parse(user).id
             console.log(this.phone)
        }
	}
</script>

<style lang="scss" scoped>

.swiper-inner {
    width: 100%;
  }
  .swiper-slide {
    background-position: center;
    background-size: cover;
  }
	.reservedInformation{
		width: 100%;
		height: 100%;
		background: #f3f3f3;
		position: relative;
		.information_header{
  	width: 100%;
	height: 2.85rem;
  	background: #fae44c;
  	padding-left: 0.62rem;
  	padding-right: 0.62rem;
  	box-sizing: border-box;
  	display: flex;
  	justify-content: space-between;
  	 .back{
  	width: 2.62rem;
  	height: 100%;
  	background: url(../../../../assets/back.png) no-repeat center center;
  	background-size: 0.62rem 1.28rem;
 		 }
 		  .center{
  	width: 7.61rem;
  	height: 2.81rem;
  	text-align: center;
  	line-height: 2.81rem;
  	font-size: 1.12rem;
    color: #333333;
  }
  .block{
  	width: 2.62rem;
  }
 	 }
 	 .warn{
 	 	width: 100%;
 	 	height: 2.37rem;
 	 	text-align: center;
 	 	line-height: 2.37rem;
 	 	background: #ffffff;
 	  font-size: 0.71rem;
 	  color: #f94b4b;
	   img{height: 1rem;width: 1.21875rem;}
 	 }
 	 .crosswise{
 	 	width: 100%;
 	 	height: 14.875rem;
 	 	background:#f3f3f3 ;
        position: relative;
 	 	padding: .53125rem .625rem;
 	 	.swiperbox{
			width: 100%;
 	 		height:100%;
			   background: #ffffff;
 	 		.swiper-wrapper{
 	 			width: 100%;
 	 			height: 100%;
 	 		}
 	 		.carts{
 	 			width: 100%;
 	 			height: 100%;
 	 			.cart_top{
 	 				width: 100%;
 	 				height: 2.43rem;
 	 				font-size: 0.78rem;
 	 				line-height: 2.62rem;
 	 				text-align: center;
 	 				color: #25222b; 	 				
 	 				span{
 	 					background: #4aadf0;
 	 					color: #ffffff;
 	 					border-radius: 2px;
 	 					margin-left: 0.62rem;
 	 				}
 	 			}
 	 			.cart_center{
 	 				width: 100%;
 	 				height:9.62rem;
 	 				display: flex;
 	 				flex-direction: column;
 	 				.logo{
 	 					width: 12.5rem;
 	 					height: 6.87rem;
 	 					margin: 0 auto;
 	 					text-align: center;
 	 					line-height: 3.87rem;	
						  img{height: 100%;width: 100%;}
 	 				}
 	 				.center_right{
 	 					height: 100%;
 	 					flex: 1;
 	 					.price{
 	 						width: 100%;
 	 						height: 2rem;
 	 						text-align: center;
 	 						line-height: 2.28rem;
 	 						color: #f94b4b;
 	 						font-size: 1.03125rem;
 	 						
 	 					}
 	 					.people{
 	 						width: 100%;
 	 						height: 1.06rem;
 	 						text-align: center;
 	 						line-height: 1.06rem;
 	 						font-size: 0.56rem;
 	 						color: #0f0f0f;
 	 					}
 	 				}
 	 				
 	 			}
 	 			.cart_bottom{
 	 				
 	 					width: 100%;
 	 					height:2.59rem;
 	 					text-align: center;
 	 					padding: 0 0.625rem;
 	 					span{
 	 						
 	 						height: 1.5rem;
 	 						line-height: 1.5rem;
 	 						font-size: 0.59rem;
 	 						color: #787878;
 	 					}
 	 					
 	 				
 	 			}
 	 		}
 	 	}
 	 }
 	 .apply_submit{
 	 	width: 100%;
 	 	height: 15.84375rem;
 	 	padding: 0 0.62rem;
 	 	
 	 	.big_box{
 	 		width: 100%;
 	 		height: 100%;
 	 		background: #ffffff;
 	 		padding:3px; 	 		
 	 		.top{
 	 			width: 100%;
 	 			height: 3.65625rem;
 	 			background: #ffffff;
 	 			text-align: center;
 	 			line-height: 3.65625rem;
 	 			font-size: .84375rem;
 	 			color: #211e27;
 	 		}
 	 		.buttom{
 	 			width: 100%;
 	 			height: 11.87rem;
 	 			.name{
 	 				width: 100%;
 	 				height: 2.81rem;
 	 				padding-top: 0.87rem;
 	 				display: flex;
 	 				span{
 	 					display: block;
 	 					width: 3.09rem;
 	 					height: 100%;
 	 					text-align: center;
 	 					line-height: 1.93rem;
 	 					font-size: 0.84rem;
 	 					color: #1a1a1a;
 	 				}
 	 				input{
 	 					height: 100%;
 	 					flex: 1;
 	 					display: block;
 	 					border: none;
 	 					
 	 					border-bottom: 1px solid #efefef;
 	 				}
 	 			}
 	 			.phone_number{
 	 				width: 100%;
 	 				height: 2.81rem;
 	 				padding-top: 0.87rem;
 	 				display: flex;
 	 				span{
 	 					display: block;
 	 					width: 3.09rem;
 	 					height: 100%;
 	 					text-align: center;
 	 					line-height: 1.93rem;
 	 					font-size: 0.84rem;
 	 					color: #1a1a1a;
 	 				}
 	 				input{
 	 					height: 100%;
 	 					flex: 1;
 	 					display: block;
 	 					border: none;
						
 	 					border-bottom: 1px solid #efefef;
 	 				}
 	 			}
 	 			.apply_submit_button{
 	 				width: 9.75rem;
 	 				height: 2.59rem;
 	 				text-align: center;
 	 				line-height: 2.59rem;
 	 				background: #f94b4b;
 	 				color: #ffffff;
 	 				font-size: 0.93rem;
 	 				border-radius: 5px;
 	 				margin: 1.75rem auto;
 	 			}
 	 		}
 	 		
 	 	}
 	 }
 	 .zheZhao{
 	 	position:fixed;
 	 	z-index: 99;
 	 	top:0rem;
 	 	left: 0;
 	 	height: 100%;
 	 	width:100% ;
 	 	background: rgba(0,0,0,0.45);
	 	 	.sureBox{
	 	 	width: 11.68rem;
	 	 	height: 11.68rem;
	 	 	position: absolute;
	 	 	background: #ffffff;
	 	 	top: 0;
	 	 	left: 0;
	 	 	bottom: 0;
	 	 	right: 0;
	 	 	margin: auto;
	 	 	border-radius: 12px;
	 	 	.sureP{
	 	 		width: 100%;
	 	 		height: 2.71rem;
	 	 		font-size: 0.81rem;
	 	 		color: #232323;
	 	 		padding-top: 1.78rem;
	 	 		text-align: center;
	 	 	}
	 	 	.applyNumber{
	 	 		width: 100%;
	 	 		height: 2.75rem;
	 	 		text-align: center;
	 	 		line-height: 2.75rem;
	 	 		font-size: 0.65rem;
	 	 		color:#232323 ;
	 	 	}
	 	 	.sureP2{
	 	 		width: 100%;
	 	 		height: 1.43rem;
	 	 		padding: 0 1.09rem;
	 	 		font-size: 0.59rem;
	 	 		text-align: center;
	 	 		color: #232323;
	 	 	}
	 	 	.toBank{
	 	 		width: 100%;
	 	 		height: 3.21rem;
	 	 		margin-top: 1.56rem;
	 	 		text-align: center;
	 	 		line-height: 3.21rem;
	 	 		background: #f94b4b;
	 	 		color: #ffffff;
	 	 		font-size: 0.84rem;
	 	 		border-bottom-left-radius: 12px;
	 	 		border-bottom-right-radius: 12px;
	 	 	}
	 	 	.chacha{
	 	 		width:1rem;
	 	 		height: 1.03rem;
	 	 		background: url(../../../../assets/sureCha.png) no-repeat center center;
	 	 		background-size: 100%;
	 	 		position: absolute;
	 	 		top: 0;
	 	 		right:-1.2rem;
	 	 	}
	 	 }
 	 }
 	  .err_num{
	  position: absolute;
	  top: 17rem;
	  left: 7rem;
	  height: 3.3375rem;
	  line-height: 3.3375rem;
	  text-align: center;
	  font-size: 0.3rem;
	  border-radius: .4125rem;
	  width: 9.5625rem;
	  background: rgba(0, 0, 0, .8);
	  color: #ffffff;
	  z-index: 100;
	  
  }
	}
</style>


