<template>
	<div class="moneyRecord">
		<div class="moneyRecord_header">
			<div class="back" @click="back"></div>
			<div class="center">现金收支记录</div>
			<div class="block"></div>
		</div>

		<mt-button @click.native="open('picker')" size="large">{{value | formatDate}}</mt-button>
		<mt-datetime-picker class="picker_show"
                            ref="picker"
                            type="date"
							year-format="{value} 年"
  							month-format="{value} 月"
							date-format="{value} 日"
                            v-model="value"
                            :startDate = "startDate"
                            :endDate = "endDate"
							@confirm="handleConfirm"
		></mt-datetime-picker>

		

		<div class="listbox">
            <div class="none" v-if="isNone">暂无收支记录</div>

			<div class="monthBox" v-if="!isNone">
			
			<div class="monthHeader">
					<span>{{remark}}</span>
				</div>

			<v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
				<div class="theMonthBigBox">
					<div class="theMonthSmollBox" v-for="datas in MRecordlist" @click="showFail(datas)">
						<div class="left"><img :src="datas.logo"/></div>
						<div class="center">
							<p>{{datas.paymentName}} <span v-if="datas.paymentStatus==2 ">-失败</span>  </p>
							<p>{{dateTimeTOString(datas.modifyTime)}}</p>
						</div>
						<div class="right" v-if="datas.paymentStatus == 1">-{{datas.money}}</div>
						<div class="right2" v-if="datas.paymentStatus == 2">+{{datas.money}}</div>
					</div> 
				</div>	
				 <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../assets/cry.png"/></div>
			</v-loadmore>

			</div>
		</div>
		<div class="failBox_big" v-show="failBox">
			<div class="failBox_smoll">
				<div class="top">
					<span> </span><span>提现结果</span><span @click="cha"></span>
				</div>
				<div class="center">失败！</div>
				<div class="failRession">{{this.failRession}}</div>
				<div class="failTime">{{this.failTime}}</div>
			</div>
		</div>
	</div>
</template>

<script>
import { Loadmore } from 'mint-ui';
import { formatDate } from '../../../js/data.js';
	export default{
		 filters: {                    //时间转换
			formatDate(time) {
			var date = new Date(time);
			return formatDate(date, 'yyyy-MM');
			}
		  },
		  components:{
    	  'v-loadmore':Loadmore,
  		  },
		data(){
			return{
					isNone:false,
				    value: new Date(),          //定义显示时间
					visible: false,
					startDate: new Date('2018-01'),      //设置开始时间
					endDate: new Date(),   //设置结束时间
				failBox:false,
				failRession:"",
				failTime:"",
				MRecordlist:[

				],
				pageNo:1,
                pageSize:8,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
				user:'',
				remark:''
			}
		},
		methods:{
			dateTimeTOString(dateTime) {  
				var source = new Date(dateTime);   
				var yy =  source.getFullYear().toString();  
				var mm = source.getMonth() + 1;  
				mm = mm < 10 ?  ('0'+mm.toString()) : mm.toString();  
				var dd = source.getDate();  
				dd = dd < 10 ?  ('0'+dd.toString()) : dd.toString();  
				var hh = source.getHours();  
				hh = hh < 10 ?  ('0'+hh.toString()) : hh.toString();  
				var MM = source.getMinutes();  
				MM = MM < 10 ?  ('0'+MM.toString()) : MM.toString();  
				var ss = source.getSeconds();  
				ss = ss < 10 ?  ('0'+ss.toString()) : ss.toString();  
				var aa = yy + '-' + mm + '-' + dd + ' ' + hh + ':' + MM + ':' + ss;  
				return aa;  
			},
			back(){
				this.$router.go(-1)
			},
			cha(){
				this.failBox = false
			},
			showFail(datas){			
				if (datas.paymentStatus == 2 ) {
					this.failBox = true;
					this.failRession = datas.remark;
					this.failTime = this.dateTimeTOString(datas.modifyTime);
				}
			},
			open(picker) {
          		this.$refs[picker].open();    //设置开始
				},
		   handleConfirm(value){
			   console.log(value)
			 this.value = value;
			 this.loading =false
			 this.allLoaded = false					
             this.isAll =false
             this.pageNo = 0
			this.MRecordlist = []
			 this.loadBottom()
		   },
		    loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
			},
			  loadList(show){
				   let a = this.value
				   a = a.toString()
		    	    this.$http({
						method:'post',
						url:this.$cashPaymentSearch,
						 params:{
                             userId :this.user.id,
                             'page.pagesize':this.pageSize,
							 'page.currentPage':this.pageNo,
							 'searchTime' : a
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						this.remark = response.data.remark
	
						// console.log(this.value)
						if(response.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;
						}else{
							this.MRecordlist = this.MRecordlist.concat(response.data.data)
							console.log(this.remark)
						}
						if(this.MRecordlist.length == 0){
							this.isNone = true
						}else{
							this.isNone = false
						}
					}).catch((err)=>{
						console.log(err)
					})	
					
			},
			 more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
		},
		mounted(){
			this.loadList(false);
		},
		beforeMount(){
			this.user = JSON.parse(localStorage.getItem("userId"))
	
		},
		updated(){
			
		}
	}
</script>

<style scoped lang="scss">
.moneyRecord{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	.moneyRecord_header{				
			  	width: 100%;
				height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #333333;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 
		
	}
		.monthHeader{
			width: 100%;
			height: 2.31rem;
			background: #ffffff;
			display: flex;
			padding-left:0.62rem;
			border-bottom: 1px solid #ababab;
			span{
				display: block;
				height: 100%;
				line-height: 2.31rem;
				color: #fe0000;
				font-size: 0.81rem;
			}
			span:nth-child(1){
				margin-right: 1.56rem;
			}
		}
	.listbox{
		width: 102%;
		padding-right: 2%;
		overflow-x: hidden;
		flex: 1;
		 .none{
            height: 50%;
            width: 100%;
            text-align: center;
            position: absolute;
            top: 50%;
            color: #040404;
        }
	
		.theMonthBigBox{
			width: 100%;
			padding: 0 0.62rem;
			background:#ffffff;
			.theMonthSmollBox{
				height:4.06rem ;
				width: 100%;
				border-bottom: 1px solid #aaaaaa ;
				padding-top: 0.71rem;
				display:flex;
			.left{
				width: 3.21rem;
				height: 100%;
				img{
					display: block;
					width: 2.59rem;
					height: 2.59rem;
					margin-left:0.31rem ;
				}
			}
			.center{
				margin-left: 1.28rem;
				width: 10.59rem;
				height:100%;
				p:nth-child(1){
					width: 100%;
					height:2.09rem;
					line-height: 2.09rem;
					color: #2a252c;
					font-size: 0.84rem;
				}
			}
			.right{
				flex: 1;
				height: 2.09rem;
				text-align: right;
				line-height: 2.09rem;
				font-size: 0.75rem;
				color: #363636;
			}
			.right2{		
				flex: 1;
				height: 2.09rem;
				text-align: right;
				line-height: 2.09rem;
				font-size: 0.75rem;
				color: #fe0000;			
			}
		}
		}
		
	}
	.failBox_big{
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,.5);
		position: absolute;
		top: 0;
		left: 0;
		z-index: 10;
		display: flex;
		justify-content: center;
		align-items: center;
		.failBox_smoll{
			width: 12.9rem;
			height: 13.87rem;
			border-radius: 10px;
			overflow:hidden;
			background:#ffffff;
			.top{
				width: 100%;
				height: 2.12rem;
				background: #8237e9;
				display: flex;
				padding: 0 0.62rem;
				span{
					height: 100%;
					line-height: 2.12rem;
					color: #ffffff;
					font-size: 0.78rem;
					display: block;
					flex: 1;
				}
				span:nth-child(3){
					background: url(../../../assets/sureCha.png) no-repeat right center;
					background-size: 0.96rem 0.96rem;
					}
			}
			.center{
				width: 100%;
				height: 1.84rem;
				margin-top: 1.68rem;
				text-align: center;
				line-height: 1.84rem;
				color: #fe0000;
				font-size:1.4rem; 
			}
			.failRession{
				width: 100%;
				height: 2.09rem;
				padding: 0 1.56rem;
				margin-top: 0.71rem;
				font-size:0.65rem; 
				line-height: 1.18rem;
				color: #4c4c4c;
			}
			.failTime{
				width: 100%;
				height: 0.81rem;
				padding: 0 1.56rem;
				text-align: center;
				line-height: 0.81rem;
				font-size: 0.81rem;
				color:#4c4c4c ;
				margin-top: 3.34rem;
			}
		}
	}
}
	
</style>


