<template>
    <div class="box_1">
    	<div class="meiyou" v-if="haveNo">暂无任务敬请期待</div>
        <div class="box_2" v-if="!haveNo">
         <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
           <div class="content" v-for="data in datalist" :key="data.id" @click="getDetail(data)">
               <div class="name"><span>{{data.taskName}}</span><p>{{data.cardType}}</p></div>
               <div class="box">
                <div class="card"><img :src="'http://'+data.taskLogo" alt=""></div>
                <div class="mid"> 
                    <div class="feature"><img src="../../../../assets/star.png" alt=""> {{data.taskDescription}}</div>
                    <!--<div class="feature"><img src="../../../../assets/star.png" alt=""> {{data.taskDescription}}</div>-->
                </div>
                <div class="price">{{parseInt(data.reward)}}元</div>
                </div>
           </div>
           <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
         </v-loadmore> 

        </div>
    </div>
</template>

<script>
//任务列表部分
    import { Loadmore } from 'mint-ui';
    export default {
        components:{
    	  'v-loadmore':Loadmore,
  		  },
  		  props:["listOrder","moneyOrder"],
  		  watch:{
  		  	listOrder(newValue,oldValue){
  		  		console.log(newValue)
  		  	},
  		  	moneyOrder(newValue,oldValue){			
  		  		this.Worder= newValue
				console.log(this.Worder)
				if(this.Worder == "11"){
					this.datalist = []
					this.ascDesc = true
					this.whichProperty = 'reward'
					this.loading =false
                	this.isAll=false
                	this.allLoaded = false
                	this.pageNo = 0					 
					this.loadBottom()
				}else if(this.Worder == "12"){
					this.datalist = []
					this.ascDesc = false
					this.whichProperty = 'reward'					
					this.loading=false
                	this.isAll =false
                	this.allLoaded = false
					this.pageNo = 0              	
					this.loadBottom()
				}else{
					this.datalist = []
					this.ascDesc = null	
					this.whichProperty = null										
					this.loading =false
					this.allLoaded = false					
                	this.isAll =false
                	this.pageNo = 0                	
					this.loadBottom()
					
				}
  		  	}
  		  },
        name:"GetcardMissionList",
        data(){
            return{
                datalist:[
                    // {logo:"",name:"卡片名称10",type:"卡片类型",feature1:"信用卡特点1信用卡特点1信用卡特点1",feature2:"信用卡特点2信用卡特点2信用卡特点2",price:"30元",id:"14"}
                    ],
                pageNo:1,
                pageSize:7,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
                Worder:"",
                ascDesc:null,
                whichProperty:null,
                haveNo:false
            }
        },
        methods:{
            getDetail(data){
               this.$router.push('/makemoney/banka/getcardmission/reservedInformation'+"/"+data.id)
            },
            loadBottom() {
              event.preventDefault();
			  event.stopPropagation();
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	this.$http({
						method:'post',
						url:this.$taskSearch,
						 params:{
							 taskType:3,
							 ascDesc:this.ascDesc,
							 whichProperty:this.whichProperty,
							 "page.pagesize":this.pageSize,
							 "page.currentPage":this.pageNo
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						console.log(response)
						if(response.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;
							
						}else{
							this.datalist = this.datalist.concat(response.data.data);
							if(response.data.data.length == 0){
								this.haveNo = true
							}else{
								this.haveNo = false
								
							}
						}
						
					}).catch((err)=>{
						console.log(err)
					})		
		   },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
        },
        mounted(){
            this.loadList(false);
        }
    }
</script>

<style lang="scss" scoped>
    .box_1{
        flex:1;
        width: 100%;
        display: flex;
        overflow: hidden;
        .meiyou{
        	width: 100%;
        	height: 100%;
        	background: #ffffff;
        	text-align: center;
        	padding-top: 12.5rem;
        }
        .box_2{
            height: 100%;
            width: 100%;
            flex: 1;
            overflow: auto;
           
            .content{
                width: 100%;
                padding:  .9375rem .65625rem;
                background: #ffffff;
                border-bottom: .03125rem solid #eeeeee;
                height: 6.25rem;
              
                position: relative;
                .box{display: flex;}
                 .name{
                        display: flex;
                        height: 1.46875rem;
                        line-height: 1.46875rem;
                        font-size: .6875rem;
                        span{color: #010101}
                        p{width: 3.125rem;height: .75rem;line-height: .75rem;background: #4aadef;border-radius: 2px;
                        color: #ffffff;margin-top: .36875rem;margin-left:0.62rem;text-align: center;}
                    }
                .card{
                    width: 6.03125rem;
                    height: 3.34375rem;
                    img{height: 100%;width: 100%;}
                }
                .mid{
                    margin-left: .3125rem;
                    height: 3.34375rem;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    .feature{color: #9f9d9d;font-size: .625rem;margin-bottom: .1875rem;
                            height: .825rem;line-height: .825rem;
                            img{height: .375rem;width: .375rem;}
                            }
                }
                .price{
                    position: absolute;
                    right: 0.62rem;
                    top: 2.34375rem;
                    color: #f94b4b;
                    font-size: .875rem;
                    height: 3.34375rem;
                    line-height: 3.34375rem;
                }
            }
        }
    }
</style>


