<template>
    <div class="drawCashRecord">
        <div class="draw_header">
			<div class="back" @click="drawCashRecordBack"></div>
			<div class="center">提现记录</div>
			<div class="block"></div>
		</div>
		<mt-button @click.native="open('picker')" size="large">{{value | formatDate}}</mt-button>
		<mt-datetime-picker class="picker_show"
                            ref="picker"
                            type="date"
							year-format="{value} 年"
  							month-format="{value} 月"
                            v-model="value"
                            :startDate = "startDate"
                            :endDate = "endDate"
							@confirm="handleConfirm"
		></mt-datetime-picker>
		
        <div class="content_record">
            <div class="none" v-if="isNone">暂无提现记录</div>
            <div class="Notnone" v-if="!isNone">
                <div class="head">{{total}}</div>
                
                 <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
         				<div class="box_main">

                  <div class="box" v-for="items in itemlist" :key="items.id" @click="btn(items)">
                    <div class="left" :style="{background: 'url('+items.logo+') no-repeat center center'}"></div>
                    <div class="right">
                         <div class="tp" v-if="items.withdrawStatus == 1">[{{dataToWay(items.withdrawPlace)}}] 至<span>{{items.withdrawAccount}}</span><span>-{{items.money}}元</span></div>
                         <div class="tp2" v-if="items.withdrawStatus == 2">[{{dataToWay(items.withdrawPlace)}}]-失败<span>+{{items.money}}元</span></div>
                         <div class="bm">{{dateTimeTOString(items.modifyTime)}}</div>

                    </div> 
                  </div>
                </div>
         
		           <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../assets/cry.png"/></div>        
		         </v-loadmore> 
                
              
            </div>
        </div>

        <div class="fail" v-if="isfail">
            <div class="cha" @click="close">X</div>
            <div class="jd">提现进度</div>
            <div class="sb">失败！</div>
            <div class="reson">原因：{{reson}}</div>
            <div class="time">时间:{{fail_time}}</div>
        </div>
    </div>
</template>

<script>
import { formatDate } from '../../../js/data.js';
 import { Loadmore } from 'mint-ui';
	
    //提现记录页面
    export default {
        name:"DrawCashRecord",
        components:{
    	  'v-loadmore':Loadmore,
  		  },
        filters: {                    //时间转换
			formatDate(time) {
			var date = new Date(time);
			return formatDate(date, 'yyyy-MM');
			}
      	},
        data(){
            return{
            		value: new Date(),          //定义显示时间
					visible: false,
					startDate: new Date('2018-01'),      //设置开始时间
					endDate: new Date(),   //设置结束时间
					mounth:'',
					total:'',
                //是否有提现记录
                isNone:"",
                //是否失败
                isSuccess:false,
                itemlist:[],
                //失败下的弹窗参数
                isfail:"",
                fail_time:"",
                reson:"",
                //下拉加载需要的东西
                pageNo:1,
                pageSize:8,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,    
                userId:''
            }
        },
        methods: {
        	dateTimeTOString(dateTime) {  
				var source = new Date(dateTime);   
				var yy =  source.getFullYear().toString();  
				var mm = source.getMonth() + 1;  
				mm = mm < 10 ?  ('0'+mm.toString()) : mm.toString();  
				var dd = source.getDate();  
				dd = dd < 10 ?  ('0'+dd.toString()) : dd.toString();  
				var hh = source.getHours();  
				hh = hh < 10 ?  ('0'+hh.toString()) : hh.toString();  
				var MM = source.getMinutes();  
				MM = MM < 10 ?  ('0'+MM.toString()) : MM.toString();  
				var ss = source.getSeconds();  
				ss = ss < 10 ?  ('0'+ss.toString()) : ss.toString();  
				var aa = yy + '-' + mm + '-' + dd + ' ' + hh + ':' + MM + ':' + ss;  
				return aa;  
			},
			dataToWay(dataStatus){
				var bb = "";
				if(dataStatus == 1){
					bb= "支付宝提现"
				}else if(dataStatus == 2){
					bb = "微信提现"
				}else if(dataStatus == 3){
					bb = "话费充值"
				}
				return bb
			},
            drawCashRecordBack(){ 
                     this.$router.push('/my/drawCash')
                },
            btn(items){
                //若打款失败，弹出弹框 渲染弹框内内容
                if(items.withdrawStatus == 2){
                    this.isfail = true;
                    this.reson = items.remark;
                    this.fail_time = this.dateTimeTOString(items.modifyTime)
                    
                }
            },
            //关闭失败窗口
            close(){
                this.isfail = false;
            },
            open(picker) {
          		this.$refs[picker].open();    //设置开始
				},
		   handleConfirm(value){
		   		this.value = value
//		   		console.log(this.value)
			   this.itemlist = []									
					this.loading =false
					this.allLoaded = false					
                	this.isAll =false
                	this.pageNo = 0                	
					this.loadBottom()
		   },
		   loadBottom() {
              
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	let a = this.value;
		    	a = a.toString()
		    	this.$http({
						method:'post',
						url:this.$withdrawRecordSearch,
//						url:this.$withdrawRecordSearch+"?userId="+this.userId+"&searchTime="+this.value+"&'page.pagesize'="+this.pageSize+"&'page.currentPage'="+this.pageNo,
						 params:{
							 userId:this.userId,
							 searchTime:a,
							 "page.pagesize":this.pageSize,
							 "page.currentPage":this.pageNo
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						console.log(response.data)
						if(response.data.data.length == 0){ 						
							this.allLoaded = true; 
							this.isAll = show;							
						}else{
								this.total = response.data.remark
								
								this.itemlist = this.itemlist.concat(response.data.data); 
						}
						if(this.itemlist.length == 0){
							this.isNone = true;
						}else{
							this.isNone = false;
						}
						
					}).catch((err)=>{
						console.log(err)
					})		
		   },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
        },
        computed:{
           
        },
        beforeMount(){
  
        	this.userId = JSON.parse(localStorage.getItem("userId")).id
            console.log(this.userId)
        },
         mounted(){
            this.loadList(false);
//          console.log(this.value)
        }
    }
</script>

<style lang="scss" scoped>
.drawCashRecord{
   
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    .fail{
        height: 7.0625rem;width: 11.09375rem;
        border:.03125rem solid #535353;
        position: fixed;top: 40%;
        background: #fff;left: 25%;
        border-radius: .41875rem;color:#000000;
        .cha{position: relative;left: 90%;top:2%;}
        .jd{font-size: .6875rem;width: 100%;text-align: center;}
        .sb{font-size: 1.03125rem;margin-top: .875rem;width: 100%;text-align: center;}
        .reson{margin-top: .46875rem;width: 100%;text-align: center;font-size: .6875rem;}
        .time{margin-top: .46875rem;width: 100%;text-align: center;font-size: .6875rem;}
    }
    .draw_header{
        width: 100%;
	height: 2.85rem;
        background: #fae44c;
        padding-left: 0.62rem;
        padding-right: 0.62rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../../assets/back.png) no-repeat left center;
            background-size: 0.62rem 1.28rem;
        }
        .center{
            width: 4.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #72463d;
           
        }
        .block{
            width: 2.62rem;
        }
    }
    .content_record{
    	background: #ffffff;
        width: 100%;
        flex: 1;
        overflow: auto;
        .none{
            height: 50%;
            width: 100%;
            text-align: center;
            position: absolute;
            top: 50%;
            color: #040404;
        }
        .Notnone{
            .head{height: 2.34375rem;width: 100%;
       			 background: #ffffff;
                line-height: 2.34375rem;color: #fe0000;border-bottom:.03125rem solid #cacaca;
                span{margin-left: .625rem;margin-right: 1.78125rem;}
                }
            .box_main{
                margin: 0 .625rem;
              .box{
                height: 4rem;width: 100%;
                border-bottom: .03125rem solid #aaaaaa;
                display: flex;
                .left{
                    height: 4rem;
                    width: 3.28125rem;
                    text-align: center;
                    margin-right: .75rem;
                    background-size: 3.6rem 4.2rem !important;
                  }
                .right{
                    width: 100%;height: 100%;
                    .tp{margin-top: 1.34375rem;width: 100%;
                        font-size: 0.84rem;color: #040404;margin-bottom:.75rem;
                        span:nth-child(1){font-size: .6875rem;color: #6b6b6b;}
                        span:nth-child(2){float: right;}
                        }
                     .tp2{
                     	margin-top: 1.34375rem;width: 100%;
                        font-size: 0.84rem;color: #040404;margin-bottom:.75rem;                       
                        span{float: right;color: #fd0100;}
                        
                     }
                    .bm{font-size: .625rem;color: #6b6b6b;width: 100%;
                    	
                    }
                }
              }
            }
           
        }
    }
}
</style>


