<template>
    <div class="free_class">
        <div class="header">
            <div class="back" @click="freeClassBack"></div>
			<div class="center">免费课程</div>
            <div class="block"></div>
        </div>

        <div class="bottom">
 <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">	
            <div class="box" v-for="data in datalist" :key="data.id" @click="goDetail(data)">
                <div class="content">
                    <div class="left"><img :src="'http://'+data.goodLogo"/></div>
                    <div class="right">
                        <div class="name">{{data.goodName}}</div>
                        <div class="feature">{{data.goodTrait}}</div>
                        <div class="btm">
                            <div class="logo"><img :src="'http://'+data.platformLogo"/></div>
                            <div class="merchant">{{data.shopName}}</div>
                            <div class="num">{{data.buyNumber}}人领取</div>
                        </div>
                    </div>
                </div>
            </div>
         <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../assets/cry.png"/></div>
	</v-loadmore>	
        </div>
        
    </div>
</template>

<script>
	import { Loadmore } from 'mint-ui';
    export default {
    	components:{
    	  'v-loadmore':Loadmore,
  		},
        name:"FreeClass",
        data(){
            return{
                datalist:[],
                pageNo:1,
                pageSize:10,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false
            }
        },
        methods: {
            freeClassBack(){
                this.$router.push('/discount')
            },
            //点击转第三方
            goDetail(data){
                console.log(data.id)
                this.$router.push({
                    path:'/third',
                    query:{
                        title:"免费课程",
                        url:data.goodLink
                    }
                })
            },
            loadBottom:function() {
		      // 上拉加载
		      event.preventDefault();
			  event.stopPropagation();
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
				this.$http({
						method:'POST',
						url:this.$freeClassesSearch,
						responseType:'text/plain',
						 params:{
						 	 taskType: 1,
						 	 "page.pagesize":this.pageSize,
						 	 "page.currentPage":this.pageNo
						 }
				}).then((res)=>{
						console.log(res)
						if(res.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;							
						}else{
							this.datalist = this.datalist.concat(res.data.data)
						}
						
					})
			
			},
			more(){
			   	this.pageNo++
			   	this.loadList(true);
		   }
       },
         mounted(){
			this.loadList(false)
        	
        }
    }
</script>

<style lang="scss">
    .free_class{
         	width: 100%;
            height: 100%;
            background: #f8f8f8;      
            display: flex;
            flex-direction: column;
       
        .header{
            width: 100%;
	height: 2.85rem;
            background: #fae44c;
            padding-left: 0.62rem;
            padding-right: 0.62rem;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../assets/back.png) no-repeat center center;
            background-size: 0.62rem 1.28rem;
                }
                .center{
            width: 7.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #333333;
            }
            .block{
                width: 2.62rem;
            }
 	     }

         .bottom{
            height: 100%;
            width: 100%;
            overflow: auto;
            background: #ffffff;
            padding-bottom: 2.0625rem;
            padding-top: .5625rem;
            .box{
                height: 7.34rem;
                width: 100%;
                background: #ffffff;
                padding: .625rem .625rem 0 .625rem;
                
                .content{
                    height: 100%;width: 100%;
                    display: flex;
                    border-bottom: .03125rem solid #aaaaaa;
                    .left{height: 5.375rem;width: 5.375rem;border: .03125rem solid #959595;
                            margin-right: .25rem;
                            img{height: 100%;width: 100%;}
                            }

                    .right{
                        height: 100%;width: 75%;
                        .name{
                        height: 1.875rem;
                        width: 100%;
                        line-height: 1rem;
                        font-size: 0.87rem;
                        color:#4a4a4a;
                         overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        }
                        .feature{
                        font-size: 0.87rem;
                        color:#f85415;
                        margin-top: .875rem;
                        margin-bottom: .9375rem;
                        }
                        .btm{height: .84375rem;width: 100%;margin-top: .15625rem;font-size: .71875rem;color:#646464;
                                .logo{height: .78125rem;width: .78125rem;border: .03125rem solid #646464;float: left;
                                	img{height: 100%;width: 100%;}
                                	}
                                .merchant{float: left;margin-left: 1.3125rem;font-size: 0.75rem;color: 6f6e6e;}
                                .num{float: right;font-size: 0.75rem;color: 6f6e6e;}
                            }

                    }

                }
            }
        }

    }
</style>


