<template>
	<div class="bind_alipay">
		<div class="alipay_header">
			<div class="back" @click="alipay_back"></div>
			<div class="center">支付宝绑定</div>
			<div class="block"></div>
		</div>
		<div class="ali_number">
			<span>支付宝账号</span>
			<input id="alipayNumber" type="text" placeholder="邮箱地址/手机号码" v-model="alipayAccount"/>
		</div>
		<div class="ali_name">
			<span>收款人姓名</span>
			<input id="alipayName" type="text" placeholder="收款人姓名" v-model="alipayRealName"/>
		</div>
		<div class="ali_submint" @click="bindAli"><span>提交</span></div>
	</div>
</template>

<script>
import { Toast } from 'mint-ui';	
	export default{
		data(){
			return{
				phone_number:"",
				alipayAccount:'',
				alipayRealName:''
			}
		},
		methods:{
			alipay_back(){
				this.$router.go(-1)
			},
			bindAli(){
				let regAliPhone = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
				let regAliEmail = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
				let regAliName = /^([\u4e00-\u9fa5]){2,7}$/;
				
				let phoneIsTrue = regAliPhone.test(this.alipayAccount)
				let emailIsTrue = regAliEmail.test(this.alipayAccount)
				
				if(this.alipayAccount==""||this.alipayRealName==""){//判断输入是否为空
					let instance = Toast('请输入完整信息');
					setTimeout(() => {
					instance.close();
					}, 2000);
				}else if(phoneIsTrue == false && emailIsTrue == false){
					let instance = Toast('请输入正确支付宝账号');//判断是手机号或者邮箱输入正确
					setTimeout(() => {
					instance.close();
					}, 2000);
				}else if(!(regAliName.test(this.alipayRealName))){//判断名字是否合法
					let instance = Toast('请输入正确收款人姓名');
					setTimeout(() => {
					instance.close();
					}, 2000);
				}
				else{
					 this.$http({
						method:'post',
						url:this.$boundAliPay,
						params:{
							phone:this.phone_number,
							alipayAccount:this.alipayAccount,
							alipayRealName:this.alipayRealName							
						},
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((res)=>{
						localStorage.setItem('userId',JSON.stringify(res.data.data))
					})
				}
				
				
				
			}
		},
		beforeMount(){
			var phone_number = localStorage.getItem("userId");
			  phone_number = JSON.parse(phone_number).phone
				this.phone_number = phone_number;
		}
	}
</script>

<style lang="scss" scoped>
.bind_alipay{
	width: 100%;
	height: 100%;
	background: #f8f8f8;
	.alipay_header{
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 }
 	 .ali_number{
 	 	width: 100%;
 	 	height: 3.46rem;
 	 	padding: 0  0.62rem;
 	 	padding-top: 1.46rem;
 	 	display: flex;
 	 	span{
 	 		display: block;
 	 		width: 4.43rem;
 	 		height: 100%;
 	 		text-align: center;
 	 		line-height:2rem;
 	 		color: #282828;
 	 		font-size: 0.87rem;
 	 	}
 	 	input{
 	 		display: block;
 	 		border: none;
 	 		width: 17.09rem;
 	 		height: 100%;
 	 		border-bottom: 0.03rem solid #c1c1c1;
 	 		background: #f8f8f8;
 	 		margin-left: 0.62rem;
 	 	}
 	 	#alipayNumber::-webkit-input-placeholder{
 	 		color: #d0d0d0;
 	 		font-size: 0.93rem;
 	 	}
 	 }
 	 .ali_name{
 	 	width: 100%;
 	 	height: 3.46rem;
 	 	padding: 0  0.62rem;
 	 	padding-top: 1.46rem;
 	 	display: flex;
 	 	span{
 	 		display: block;
 	 		width: 4.43rem;
 	 		height: 100%;
 	 		text-align: center;
 	 		line-height:2rem;
 	 		color: #282828;
 	 		font-size: 0.87rem;
 	 	}
 	 	input{
 	 		display: block;
 	 		border: none;
 	 		width: 17.09rem;
 	 		height: 100%;
 	 		border-bottom: 0.03rem solid #c1c1c1;
 	 		background: #f8f8f8;
 	 		margin-left: 0.62rem;
 	 	}
 	 	#alipayName::-webkit-input-placeholder{
 	 		color: #d0d0d0;
 	 		font-size: 0.93rem;
 	 	}
 	 }
 	 .ali_submint{
 	 	width: 100%;
 	 	height:2.03rem;
 	 	padding: 0 0.62rem;
 	 	margin-top: 1.87rem;
 	 	span{
 	 		display: block;
 	 		width: 100%;
 	 		height: 100%;
 	 		text-align: center;
 	 		line-height: 2.03rem;
 	 		background: #f94b4b;
 	 		color: #ffffff;
 	 		font-size: 0.84rem;
 	 		border-radius: 5px;
 	 	}
 	 }
}
</style>


