<template>
    <div class="inviteRecord">
        <div class="inviteRecord_header">
			<div class="back" @click="back"></div>
			<div class="center">邀请记录</div>
			<div class="block"></div>
		</div>
		<div class="inviteMount">
			<div><span>今日邀请{{TodayInvite}}位</span><span>累计邀请{{AllInvite}}位</span></div>
			<div><span>今日获得金币{{TodayCoin}}个</span><span>累计获得金币{{AllCoin}}个</span></div>
		</div>

		<div class="inviteContent">
			<div class="invitData" v-for="data in datalist" :key="data.id">
				<span>{{data.time}}</span>
				<div><img :src=data.img></div>
				<span>{{data.nickname}}</span>
				<span>{{data.relation}}</span>
			</div>
		</div>
    </div>
</template>

<script>
    export default {
        name:'inviteRecord',
        data(){
            return{
				TodayInvite:30,
				TodayCoin:1000,
				AllInvite:300,
				AllCoin:1000,
				datalist:[
					{time:'2018-3-15',img:'../../../static/head.png',nickname:'小胖小胖真的胖',relation:'徒弟',id:'1'},
					{time:'2018-3-15',img:'../../../static/head.png',nickname:'小胖小胖真的胖',relation:'徒弟',id:'2'},
					{time:'2018-3-15',img:'../../../static/head.png',nickname:'小胖小胖真的胖',relation:'徒弟',id:'3'}
					]
            }
        },
        methods:{
			back(){
				this.$router.go(-1)
			}
        }
    }
</script>

<style scoped='' lang='scss'>
    .inviteRecord{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        background: #f1f1f1;
		overflow: hidden;
        .inviteRecord_header{
            width: 100%;
				height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
        }
		.inviteMount{
			height: 2.375rem;
			width: 100%;
			padding: .1875rem .625rem;
			background: #f94b4c;
			color: #ffffff;
			div{display: flex;
				justify-content: space-between;
				span{font-size: .71875rem;line-height: 1rem;}
				}
		}
		.inviteContent{
			height: 100%;
			width: 100%;
			overflow-y: scroll;
			padding-top: .3125rem;
			.invitData{
				height: 2.5rem;
				line-height: 2.5rem;
				padding: 0 .625rem;
				font-size: .90625rem;
				color: #454545;
				position: relative;
				span:nth-child(1){
					margin-right: 1.4375rem;
				}
				div{height: 1.25rem;width: 1.25rem;
					margin-right: 1.71875rem;
					display: inline-block;
					img{height: 1.25rem;width: 1.25rem;}
					}
				span:nth-child(4){
					position: absolute;
					right: .625rem;
				}
			}
		}
    }
</style>


