<template>
    <div class="share">
        <div class="share_header">
			<div class="back" @click="back"></div>
			<div class="center">第三方应用</div>
			<div class="block"></div>
		</div>
        <div class="content">
            和你分享一款省钱赚钱的APP，每天都能赚钱，我在米多星已经386天，天天都在上面赚零花钱，更有现金红包免费领
        </div>
    </div>
</template>

<script>
    export default {
        name:'share',
        data(){
            return{

            }
        },
        methods:{
            back(){
                this.$router.go(-1)
            }
        }   
    }
</script>

<style scoped='' lang='scss'>
    .share{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        background: #f1f1f1;
		overflow: hidden;
         .share_header{
            width: 100%;
				height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
              }
        .content{
            height: 100%;
            width: 100%;
            padding: 2.34375rem 5.1875rem 0 4.5rem;
            font-size: .9rem;
            line-height: 1.15625rem;
            color: #363636;
        }
    }
</style>


