<template>
	<div class="aboutUs"> 
		<div class="aboutUs_header">
			<div class="back" @click="back"></div>
			<div class="center">关于米多星</div>
			<div class="block"></div>
		</div>
		<div class="logo">
			
		</div>
		<div class="verssion">
			{{verssion}}
		</div>
		<p class="pOne">米多星致力于打造一个为广大用户提供一个在闲暇时间动动手指就能得到真正实惠的平台而努力</p>
		<div class="mdx">
			<div class="to_weiChat" @click="copy">miduoxing</div>
			<p>·轻点上方“miduoxing”账号按钮，复制公众号账号，前往微信搜索，进行关注</p>
			<p>·关注公众号后可以第一时间接收新任务资讯，避免任务被其他人抢走哦~</p>
		</div>
		
	</div>
	
</template>

<script>
import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				verssion:"微信公众号"
			}
		},
		methods:{
			back(){
				this.$router.go(-1)
			},
			copy(){
				var oInput = document.createElement('input');
				oInput.value = "miduoxing";
				document.body.appendChild(oInput);
				oInput.select(); // 选择对象
				document.execCommand("Copy"); // 执行浏览器复制命令
				oInput.className = 'oInput';
				oInput.style.display='none';
				let instance = Toast('复制成功');
						setTimeout(() => {
						instance.close();
						}, 2000);
			}
		}
	}
</script>

<style lang="scss" scoped>
.aboutUs{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background: #f1f1f1;
	.aboutUs_header{				
			  	width: 100%;
				height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #333333;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 
		
	}
	.logo{
		width: 6.25rem;
		height: 6.25rem;
		margin: 0 auto;
		margin-top:2.25rem;
		background: url(../../assets/logo.png) no-repeat center center;
		background-size:  100%;
		border-radius: 18px;
	}
	.verssion{
		width: 100%;
		height: 2.93rem;
		text-align: center;
		line-height: 2.93rem;
		font-size: 0.93rem;
		color: #232323;
	}
	.pOne{
		width: 100%;
		height: 4.31rem;
		padding:0.93rem 1.25rem;
		line-height: 1.25rem;
		font-size: 0.93rem;
		color:#181818 ;
	}
	.mdx{
		height: 9.25rem;
		width: 96%;
		margin: 0 auto;
		background: #ffffff;
		padding: 0 .3125rem 0 .5rem;
		.to_weiChat{
			width: 7.15625rem;
			height: 1.4375rem;
			margin: 0.84rem auto;
			border-radius: .25rem;
			text-align: center;
			line-height:1.4375rem;
			font-size: 0.9rem;
			color: #2a2a2a;
			border: .03125rem dashed #ff0d00;
		}
		p{
			margin-bottom: .6875rem;
			font-size: .8125rem;
			color: #2b2b2b;
			line-height: 1.09375rem;

		}
	}
	
}

</style>


