<template>
    <div class="team">
        <div class="teamHead"></div>
        <div class="teamBox">
            <div class="box_center">
                <div class="content">
                    <div class="left">
                        <div class="top1">金币收益</div>
                        <div class="btm1">累计{{this.gold}}个</div>
                    </div>
                    <div class="right">
                        <div class="top2">团队成员</div>
                        <div class="btm2">徒弟{{this.tudi}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;徒孙{{this.tusun}}</div>
                    </div>
                </div>
            </div>
        <div class="teamBtn" @click="invite"></div>
        </div>
        <div class="rules"></div>
        <div class="inviteHead">
            <div>邀请榜单</div>
            <div>名次每10分钟更新1次</div>
        </div>
        <div class="Head2">
				<span>排名</span>
				<span>头像</span>
				<span>昵称</span>
				<span>邀请人数</span>
				<span>累计奖励金币</span>
		</div>

        <div class="paihangContent">
			<div class="cup">
				<div class="one"></div>
				<div class="two"></div>
				<div class="three"></div>
			</div>
            <div class="userTop" v-for="data in datas" :key="data.id">
					<div class="userHeadPic"><img :src="'http://'+data.userHeadPic" /></div>
					<div class="nicName">{{data.userName}}</div>
					<div class="taskNumber">{{data.taskNumber}}</div>
					<div class="amount">{{data.amount}}</div>
			</div>
		</div>	

        <div class="mubu" v-if="isShow">
            <div class="Dialog">
                <div class="top">邀请好友<div class="cha" @click="close"></div></div>
                <div class="mid">
                    <div><img src="../../assets/ywx.png" alt=""><p>微信</p></div>
                    <div><img src="../../assets/yqq.png" alt=""><p>QQ</p></div>
                    <div><img src="../../assets/ypyq.png" alt=""><p>朋友圈</p></div>
                    <div><img src="../../assets/ykj.png" alt=""><p>QQ空间</p></div>
                    <div><img src="../../assets/yewm.png" alt="" @click="QRCode"><p>二维码收徒</p></div>
                    <div><img src="../../assets/ylj.png" alt=""><p>复制链接</p></div>
                </div>
                <div class="copy" @click="copy">轻点复制我的邀请码 {{this.myInviteCode}}</div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name:'team',
        data(){
            return{
                isShow:false,
                //榜单
                datas:[],
                //金币累计
                gold:0,
                //徒弟数量
                tudi:0,
                //徒孙数量
                tusun:0,
                //我的邀请码
                myInviteCode:1234567
            }
        },
        methods:{
            invite(){
                console.log('邀请好友')
                this.isShow = true;
            },
            close(){
                this.isShow = false;
            },
            copy(){
                console.log("点击复制邀请码")
            },
            //二维码收徒
            QRCode(){
                this.$router.push('/MyPoster')
            }
        },
        mounted(){
            this.$http({
					method:'POST',
					url:this.$getTop,
			}).then((res)=>{
				this.datas = res.data.data
		
			})
        }
    }
</script>

<style scoped='' lang='scss'>
    .team{
        height: 100%;width: 100%;
        overflow: hidden;
        overflow-y: scroll;
        .teamHead{
            height: 6.8125rem;
            width: 100%;
            background: url('../../assets/teamHead.png') no-repeat center center;
            background-size: 100% 6.8125rem;
        }
        .teamBox{
            height: 9.03125rem;
            width: 100%;
            background: #8237e9;
            overflow: hidden;
            position: relative;
            .teamBtn{
                position: absolute;
                width: 8.21875rem;
                height: 3.125rem;
                left: 7.65625rem;
                bottom: 0;
                background: url('../../assets/teamBtn.png') no-repeat center center;
                background-size: 8.21875rem 3.125rem;
            }
            .box_center{
                width: 22.1875rem;
                height: 8.0625rem;
                margin-top: .53125rem;
                margin-left: .625rem;
                overflow: hidden;
                background: #ffffff;
                .content{
                    height: 3.625rem;
                    width: 17.625rem;
                    margin-top: 1.21875rem;
                    margin-left: 3.09375rem;
                    display: flex;
                    .left{
                        width: 45.4%;height: 100%;
                        border-right: .03125rem solid #353535;
                        .top1{color: #6a6a6a;font-size: .90625rem;margin-bottom: 1.46875rem;}
                        .btm1{font-size: .90625rem;color: #fc7921;}
                        }
                    .right{
                        width: 54%;height: 100%;
                        .top2{color: #6a6a6a;font-size: .90625rem;margin-left: 4.34375rem;margin-bottom: 1.46875rem;}
                        .btm2{font-size: .90625rem;color: #fc7921;margin-left: 2.9375rem;}
                    }
                }
            }
        }
        .rules{
            height: 12.34375rem;
            width: 100%;
            background: url('../../assets/rule.png') no-repeat center center;
            background-size: 100% 12.34375rem;
        }
        .inviteHead{
            width: 100%;height: 1.25rem;
            background: #8237e9;
            line-height: 1.25rem;
            color: #ffffff;
            font-size: .78125rem;
            display: flex;
            justify-content: space-between;
            padding-left: 1.25rem;
            padding-right: .5625rem;
        }
        .Head2{
            height: .9375rem;
			line-height: .9375rem;
			font-size: .6875rem;
			color: #4d4d4d;
			margin-top: .1875rem;
				span:nth-child(1){
					margin-left: 1.40625rem;
				}
				span:nth-child(2){
					margin-left: .375rem;
				}
				span:nth-child(3){
					margin-left: 1.8125rem;
				}
				span:nth-child(4){
					margin-left: 3.625rem;
				}
				span:nth-child(5){
					margin-left: 3rem;
				}
        }
        .paihangContent{
            width: 100%;
            padding-left: 3.31rem;
            padding-right: .5625rem;
            position: relative;
            .cup{
                	width: 2.13125rem;
                    position: absolute;
                    top: 0;
                    left: 0.78rem;
                    flex-direction: column;
                    justify-content: space-between;
                    display:flex;
                    .one{
                        width: 2.71rem;
                        height: 2.0625rem;
                        background: url('../../assets/topOne.png') no-repeat center center;
                        background-size: 1.31rem 1.5rem;
                    }
                    .two{
                        width: 2.71rem;
                        height: 2.0625rem;
                        background: url('../../assets/topTwo.png') no-repeat center center;
                        background-size: 1.31rem 1.5rem;   		
                    }
                    .three{
                        width: 2.71rem;
                        height: 2.0625rem;
                        background: url('../../assets/topThree.png') no-repeat center center;
                        background-size: 1.31rem 1.5rem;   		
                    }
            }

        	.userTop{
   		width: 100%;
   		height: 1.71rem;
   		display: flex;
   		margin-bottom:0.53rem;
   		
   		.userHeadPic{
   			width: 1.75rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			img{
   				width: 1.25rem;
   				height: 1.25rem;
   			}
   		}
   		.nicName{
   			width: 4.28rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			font-size: 0.59rem;
   		}
   		.taskNumber{
   			width: 2.5rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			font-size: 0.59rem;
   			margin-left:3.59rem ;
   		}
   		.amount{
   			width: 2.06rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			font-size: 0.59rem;
   			margin-left: 4.74rem;
   		}
   	}

        }

        .mubu{
            height: 100%;
            width: 100%;
            position: fixed;
            top: 0;
            background: rgba(65, 65, 65, .65);
            .Dialog{
                height: 13.84375rem;
                width: 12.8125rem;
                background: #ffffff;
                border-radius: .5125rem;
                position: absolute;
                top: 0;left: 0;right: 0;bottom: 0;
                margin: auto;
                overflow: hidden;
                .top{
                    height: 2.0625rem;
                    line-height: 2.0625rem;
                    text-align: center;
                    color: #ffffff;
                    width: 100%;
                    background: #8237e9;
                    font-size: .96875rem;
                    position: relative;
                    .cha{
                        height: 1.09375rem;
                        width: 1.09375rem;
                        position: absolute;
                        right: .5rem;top: .4rem;
                        background: url('../../assets/sureCha.png') no-repeat center center;
                        background-size: 1.09375rem 1.09375rem;
                    }
                }
                .mid{
                    height: 9.4375rem;
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    border-bottom: .03125rem solid #8237e9;
                    div{
                        height: 4.5rem;width: 4.1rem;text-align: center;
                        img{display: inline-block;margin-top: .59375rem;}
                        p{font-size: .81875rem;color: #4c4c4c;}
                        }
                    div:nth-child(1){
                        img{height: 1.84375rem;width: 2.09375rem;margin-bottom: .5rem;}
                    }
                     div:nth-child(2){
                        img{height: 1.71875rem;width: 1.625rem;margin-bottom: .71875rem;}
                    }
                    div:nth-child(3){
                        img{height: 1.71875rem;width: 1.65625rem;margin-bottom: .71875rem;}
                    }
                    div:nth-child(4){
                        img{height: 1.875rem;width: 2rem;margin-bottom: .65625rem;}
                    }
                    div:nth-child(5){
                        img{height: 1.75rem;width: 1.625rem;margin-bottom:.77rem;}
                    }
                    div:nth-child(6){
                        img{height: 1.75rem;width: 1.65625rem;margin-bottom:.77rem;}
                    }
                }
                .copy{
                    height: 2.34375rem;
                    line-height: 2.34375rem;
                    font-size: .75rem;
                    color: #4c4c4c;
                    text-align: center;
                }
            }
        }
    }
</style>


