			}			
		},
		methods: {
			get_redbox(){
  				this.redbox=true
  			},
  			//点击叉叉让红包消失
	  	cha(){
	  		this.redbox = false
		  },
		  //关闭新人红包领取成功页面
		  closeNewRed(){
			  this.registerOk = false;
			  localStorage.removeItem('registerOk')
		  },
	  	//点击领取红包跳转到登录页面
	  	redbox_tologin(){
			  // this.$router.push("/login")
			  this.$router.push('/register')
		  },
		  to_login(){
			  this.$router.push("/login")
		  },
		  //未登录时,办卡的筛选框出来，隐藏红包
		  getChild(data){
			  if(!localStorage.getItem("userId")){
				if(data){
					this.redShow = true
				}else{
					this.redShow = false
				}
			  }

		},
		//点击跳转到办卡
		to_getcard(){
			if(!localStorage.getItem('userId')){
