			}else{
				this.$router.push('/makemoney/banka/getcardmission')
			}
		},
		//点击到蛋蛋赚
		toDanDan(){
			var that = this
             
			
			if(!localStorage.getItem('userId')){
				this.$router.push("/login")
			}else{
//				this.$router.push({path:'/third',query:{title:'签到送惊喜',url:this.thirdUrl}})
				this.userId = JSON.parse(localStorage.getItem("userId")).id
             	var myFirstPromise = new Promise(function(resolve, reject){
					
					var u = navigator.userAgent;
					    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
					    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
					if(isAndroid){
						function plusReady(){ 	
						var dd = plus.device.imei.substring(0,15)
						console.log(dd)
						resolve(dd)
						} 
						if(window.plus){ 
							plusReady(); 
						}else{ 
							document.addEventListener("plusready",plusReady,false); 
						} 	
					}else{
						//获取苹果uuid
						function plusReady(){ 	
							plus.FMTools.registerSCSDK();
							var uuid = plus.FMTools.getIDFA();
							resolve(uuid)             
						}
						if(window.plus){ 
							plusReady(); 
						}else{ 
							document.addEventListener("plusready",plusReady,false); 
						} 	

					}
					

			 });
			myFirstPromise.then(function(dd){
						var jixing = ''
						var u = navigator.userAgent;
					    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
					    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
					    
					    var mysecondPromise = new Promise(function(resolve,reject){
					    	a();
					    	function a(){
							 if (isiOS) {
							 	jixing = 1
							 	resolve(jixing)
							 }
							 if (isAndroid) {
							 	jixing = 2
							 	resolve(jixing)
							 }
						}
					    });
					  mysecondPromise.then(function(jixing){
							  	that.$http({
								method:"GET",
								url:that.$getWowanTaskUrl,
								headers: {
				   							 'Accept': '*/*'
										},
								params:{
									t:jixing,
									cuid:that.userId,
									deviceid:dd
								}
							}).then((res)=>{						
								that.src = res.data
								console.log(that.src)
								that.$router.push({path:'/third',query:{title:'游戏类',url:that.src}})
								}).catch((err)=>{
							})
					  })
				})
            
			
			
			}
		},
		//去签到送惊喜
		to_third(){
			if(!localStorage.getItem('userId')){
				this.$router.push("/login")
			}else{
				this.$router.push({path:'/third',query:{title:'签到送惊喜',url:this.thirdUrl}})
			}
		},
		to_suggest(){
			this.$router.push('/makemoney/suggestplay')
		},
		toDrowCash(){
			this.$router.push("/my/drawCash")
		}
	  	
		},
		
		beforeMount(){
       	  //渲染前判断是否领过新人红包
       	  if(localStorage.getItem("userId")){
				 this.islogin = true
				 this.redShow = false
//				 this.mybalance = localStorage.getItem('userId')
//				 this.mybalance = JSON.parse(this.mybalance).balance
				 this.phone = localStorage.getItem('userId')
				 this.phone = JSON.parse(this.phone).phone;
				 
       	  }else{
				 this.islogin = false
				 this.redShow = true
			 }
			if(localStorage.getItem('registerOk')){
				this.registerOk = true;
			}else{
				this.registerOk = false
			}
       },
       mounted(){
       		this.$http({
       			method:"POST",
       			url:this.$getBalance,
       			params:{
       				phone:this.phone
       			}
       		}).then((res)=>{
       			this.mybalance = res.data.data.toFixed(2)
       		})
       		
       			//请求排行榜
			this.$http({
					method:'POST',
					url:this.$getTop,
			}).then((res)=>{
				this.datas = res.data.data
				console.log(res)
			})
       }
	}
