<template>
    <div class="banka_1">
        <div class="banka_2">   
            <div class="nothing" v-if="isNoTask">暂无参与办卡任务</div>
            <div class="process" v-if="!isNoTask">
               <div class="top"><img src="../../../../assets/laba.png" alt=""> 提示：核卡通过后，无需激活，奖励一周内到账</div>
        <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
                <div class="bk_content" v-for="card in cardList" :key="card.id">
                     <div class="name"><span>{{card.taskName}}</span> <span>{{card.cardType}}</span> <span>当前状态: {{card.statusDepict}}</span></div>
                    <div class="bk_top">
                        <div class="style"><img :src="'http://'+card.taskLogo" alt=""></div>
                        <div class="kpinfo">
                           
                            <!-- <p>{{card.cardStyle}}</p> -->
                            <p>姓名：{{card.name}}</p>
                            <p>手机：{{card.phone}}</p>
                            <p>时间：{{dateTimeTOString(card.modifyTime)}}</p>
                            <span class="award" v-if="card.status==7">+{{card.reward}}元</span>
                        </div>
                    </div>
                    <div class="bk_btm">
                        <div class="btn" v-if="card.status==2" @click="check_pr(card)">进度查询</div>
                        <div class="btn" v-if="card.status==4" @click="receive_award(card)">领取奖励</div>
                        <div class="btn" v-if="card.status==3" @click="check_receive(card)">查看原因</div>
                        <div class="btn" v-if="card.status==6" @click="check_get(card)">查看原因</div>
                    </div>
                </div> 
                <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
        </v-loadmore>         
            </div>
        </div>

        <div class="prompt_box" v-if="isShadow"></div>
            <div class="box_list" v-if="isBoxShow">
                <div class="list_top">
                    <p>不通过原因</p>
                    <p>由于{{this.reson}}导致申请不通过</p>
                    <p>注意： 多次申请不通过会影响申请人的征信，请珍重考虑后再申请！</p>
                </div>
                <div class="list_btm">
                    <div @click="list_close">关闭</div>
                    <div @click="re_apply">重新申请</div>
                </div>
             </div>
            

            <div class="box_list" v-if="isBoxsShow">
                <div class="list_top">
                    <p>不通过原因</p>
                    <p>由于{{this.reson}}导致申请不通过</p>
                    <p>注意： 多次申请不通过会影响申请人的征信，请珍重考虑后再申请！</p>
                </div>
                <div class="list_btm">
                    <div @click="redo_task">重做任务</div>
                    <div @click="re_upload">重新上传</div>
                </div>
             </div>
        

    </div>
</template>

<script>
//办卡进度部分
	import { Loadmore } from 'mint-ui';
    export default {
    	components:{
    	  'v-loadmore':Loadmore,
  		  },
        name:"MyGetcardMission",
        data(){
            return{
                id:'',
                //幕布
                isShadow:'',
                //弹窗1是否出现
                isBoxShow:'',
                //弹窗2是否出现
                isBoxsShow:'',
                reson:'xxxxxxxxxx',
                isNoTask:"",
                cardList:[],
                pageNo:1,
                pageSize:4,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
                user:''
            }
        },
        methods:{
            dateTimeTOString(dateTime) {  
				var source = new Date(dateTime);   
				var yy =  source.getFullYear().toString();  
				var mm = source.getMonth() + 1;  
				mm = mm < 10 ?  ('0'+mm.toString()) : mm.toString();  
				var dd = source.getDate();  
				dd = dd < 10 ?  ('0'+dd.toString()) : dd.toString();  
				var hh = source.getHours();  
				hh = hh < 10 ?  ('0'+hh.toString()) : hh.toString();  
				var MM = source.getMinutes();  
				MM = MM < 10 ?  ('0'+MM.toString()) : MM.toString();  
				var ss = source.getSeconds();  
				ss = ss < 10 ?  ('0'+ss.toString()) : ss.toString();  
				var aa = yy + '-' + mm + '-' + dd + ' ' + hh + ':' + MM + ':' + ss;  
				return aa;  
			},
            check_pr(card){
                console.log("进度查询")
            },
            //领取奖励
            receive_award(card){
                let taskId = card.taskId;
            	console.log(taskId)
                this.$router.push({path:'/makemoney/jisu/fromRegister/formMessionList/sendPhoto'+"/"+taskId})
                let info = JSON.stringify({name:card.taskName,reward:card.reward})
                localStorage.setItem('task',info)
            },
            check_receive(card){
                console.log("查看申请失败原因")
                this.isBoxShow = true;
                this.isShadow = true;
                this.id = card.taskId;
                console.log(this.id)
            },
            check_get(card){
                console.log("查看领取失败原因")
                this.isBoxsShow = true;
                this.isShadow = true;
                this.id = card.taskId;
            },
            //弹窗按钮，关闭
            list_close(){
                this.isBoxShow = false;
                this.isShadow = false;
            },
            //弹窗按钮 重新申请
            re_apply(){
                this.isBoxShow = false;
                this.isShadow = false;
                console.log(this.id)
                this.$router.push('/makemoney/banka/getcardmission/reservedInformation'+"/"+this.id)
            },
            //重做任务 跳第三方
            redo_task(){
                this.isShadow = false;
                this.isBoxsShow = false;
            },
            //重新上传  跳领取奖励页面
            re_upload(){
                let taskId = this.id;
            	console.log(taskId)
            	this.$router.push({path:'/makemoney/jisu/fromRegister/formMessionList/sendPhoto'+"/"+taskId})
            },
            loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	    this.$http({
						method:'post',
						url:this.$taskRecordSearch,
						 params:{
                             userId : this.user.id,
                             taskType : 3,
                             "page.pagesize":this.pageSize,
							 "page.currentPage":this.pageNo
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
                        console.log(response.data.data)
                        if(response.data.data.length == 0){
                            this.allLoaded = true; 
							this.isAll = show;
                            // console.log("null")
                            if(this.cardList.length == 0){
                                this.isNoTask = true
                            }else{
                                this.isNoTask = false
                            }

                        }else{
                            this.cardList = this.cardList.concat(response.data.data);
                        }
					}).catch((err)=>{
						console.log(err)
                    })	
		    },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
        },
        beforeMount(){
           
        },
        mounted(){
            let user = localStorage.getItem("userId")
            user = JSON.parse(user)
            this.user = user
            this.loadList(false);

          
        }
    }
</script>

<style lang="scss">
    .banka_1{
        flex:1;
        width: 100%;
        display: flex;
        overflow: auto;
        .prompt_box{
            height: 100%;width:100%;position: absolute;background: rgba(0, 0, 0, 0.45);top: 0;}
            .box_list{
                height: 15.40625rem;width: 14.75rem;
                position: absolute;    
                top: 35%;left: 19%;
                background: url("../../../../assets/tanchuang.png") no-repeat center center;
                background-size: 14.75rem 15.40625rem;
                border-radius: .5625rem;
                .list_top{height: 7.5625rem;width: 100%;padding: 0 1rem;
                        p:nth-child(1){height: 1.375rem;line-height: 1.375rem;font-size: .6875rem;color: #ffffff;font-size: .9375rem;
                                        margin-bottom: .3rem;text-align: center;margin-top: 5.03125rem;}
                        p:nth-child(2){width: 100%;overflow: hidden;font-size: .6875rem;line-height: 1.125rem;margin-top: 1.3rem;margin-bottom: .7rem;}
                        p:nth-child(3){font-size:.5625rem;line-height: 1rem;color: #ff0000;}
                        }
                .list_btm{display: flex;
                        div{width: 50%;text-align: center;height: 1.84375rem;line-height: 2.84375rem;}
                        div:nth-child(1){}
                        }

            }
       
        .banka_2{
            height: 100%;
            width: 100%;
            flex: 1;
            overflow: auto;
            .nothing{
                height: 45%;
                width: 100%;
                position: absolute;
                top: 55%;
                color: #5c5c5c;
                font-size: .875rem;
                text-align: center;
            }
            .process{
                height: 100%;
                width: 100%;
                background: #ffffff;
                .top{
                    height: 1.93rem;
                    line-height: 1.875rem;
                    width: 100%;
                    text-align: center;
                    font-size: .75rem;
                    color: #fa0300;
                    img{height: 1rem;width: 1.1875rem;}
                }
                .bk_content{
                    height: 8.375rem;
                    background: #ffffff;
                    width: 100%;
                    // border-top: .03125rem solid #e1e1e1;
                    box-shadow: 0rem 0.215rem 0.625rem #888686;
                    // border-bottom: .03125rem solid #6a6969;
                    // margin-bottom: .46875rem;
                    padding: 0 .625rem;
                    overflow: hidden;
                    border-bottom: .03125rem solid #efefef;
                    .name{color: #010101;font-size: .71875rem;
                        height: 1.40625rem;line-height: 1.40625rem;
                        span:nth-child(1){
                        	color: black;
                        	float: left;
                        }
                        	span:nth-child(2){
                        		float: left;
                        		    width: 3.125rem;
								    height: .95rem;
								    line-height: .95rem;
								    background: #4aadef;
								    color: #ffffff;
								    margin-top: .16875rem;
								    text-align: center;
								    margin-left:0.62rem;
								    border-radius: 2px;
                        	}
                                    span:nth-child(3){float: right;color: #f94b4b;}
                                    }
                    .bk_top{
                        height: 3.4375rem;width: 100%;
                        
                        margin-top: .34375rem;
                        display: flex;
                        .style{height: 3.4375rem;width: 6.09375rem;margin-right: .4375rem;
                                img{height: 100%;width: 100%;}
                                }
                        .kpinfo{width: 100%;height: 100%;position: relative;
                                display: flex;flex-direction: column;
                                justify-content: space-between;
                                .award{position: absolute;right: 0;top: 2rem;color: #f94b4b;font-weight: bold;}
                                p:nth-child(1){font-size: .5625rem;color: #010101;line-height: .78125rem;}
                                p:nth-child(2){font-size: .75rem;color: #010101;line-height: .9375rem;}
                                p:nth-child(3){font-size: .75rem;color: #9a9a9a;line-height: .9375rem;}
                                
                                }
                    }
                    .bk_btm{
                        height: 1.5625rem;width: 100%;margin-top: .5rem;
                        .btn{height: 1.5625rem;line-height: 1.5625rem;width: 3.4375rem;text-align: center;
                            font-size: .75rem;color: #ffffff;background: #4ca8eb;float: right;border-radius: .1375rem;
                            }
                    }
                }
            }
        }
    }
</style>


