// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import MintUI  from 'mint-ui'
import 'mint-ui/lib/style.css'

import { Toast } from 'mint-ui';

import axios from 'axios'
//300毫秒延迟
//import fastclick from 'fastclick' 
//fastclick.attach(document.body)

Vue.prototype.$http = axios

Vue.use(MintUI)

Vue.config.productionTip = false

var qz = "http://cloud.hzqimiao.com"
//登录接口的url
Vue.prototype.$login = qz+":8099/user/login";
//注册接口的 短信、注册 url
Vue.prototype.$Regist_Msg = qz+":8099/sms/getRegisterCode/";
Vue.prototype.$Regist = qz+":8099/user/register";
//修改密码的url
Vue.prototype.$ChangePsw = qz+":8099/user/changePassword";
//忘记密码的 短信、忘记 url
Vue.prototype.$ForgetPsw_Msg = qz+":8099/sms/getChangePasswordCode/";
Vue.prototype.$ForgetPsw = qz+":8099/user/forgetPassword";

//手机换绑 短信 url
Vue.prototype.$ChangeBind_Msg = qz+":8099/sms/getChangePhoneCode/";
//旧手机验证url
Vue.prototype.$ChangeBindOld = qz+":8099/user/oldPhoneValidate";
//新手机验证url
Vue.prototype.$ChangeBindNew = qz+":8099/user/changePhone";

//获取任务详情
Vue.prototype.$getTaskDetail = qz+":8099/task/getTaskDetail";
//参与任务
Vue.prototype.$partakeTask = qz+":8099/task/partakeTask";
//任务记录列表
Vue.prototype.$taskRecordSearch = qz+":8099/task/taskRecordSearch";
//搜索任务列表
Vue.prototype.$taskSearch = qz+":8099/task/taskSearch";
//红包领取接口
Vue.prototype.$getRed = qz + ":8099/user/getRedEnvelope/";
//获取余额接口
Vue.prototype.$getBalance = qz + ":8099/user/getBalance";
//获取任务排行榜
Vue.prototype.$getTop = qz + ":8099/task/getTop";
//优惠商城列表
Vue.prototype.$discountMallSearch = qz +":8099/discount/discountMallSearch";
//优惠券列表
Vue.prototype.$couponSearch = qz +":8099/discount/couponSearch";
//免费课程列表 
Vue.prototype.$freeClassesSearch = qz +":8099/discount/freeClassesSearch";

//上传注册任务凭证接口
Vue.prototype.$postPic = qz +":8099/task/registerVoucher";

//用户领取奖励接口
Vue.prototype.$getReward = qz +":8099/task/getReward";

//蛋蛋赚接口
Vue.prototype.$getWowanTaskUrl = qz +":8099/partner/api/getWowanTaskUrl/";
//绑定支付宝
Vue.prototype.$boundAliPay = qz +":8099/user/boundAliPay";
//支付宝提现接口
Vue.prototype.$testTransfer = qz +":8099/user/boundAliPay";

//提现记录接口
Vue.prototype.$withdrawRecordSearch = qz +":8099/record/withdrawRecordSearch";
//现金交易记录
Vue.prototype.$cashPaymentSearch = qz +":8099/record/cashPaymentSearch";


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


   document.addEventListener('plusready', function() {
   	 // 声明的JS“扩展插件别名”
                    var _BARCODE = 'FMTools',
                    B = window.plus.bridge;
                    var FMTools ={
                        // 声明同步返回方法
                        getUUID : function (){
                            return B.execSync(_BARCODE, "getUUID", []);
                        },           
                        registerSCSDK : function (){
                            return B.execSync(_BARCODE, "registerSCSDK", []);
                        },
                        getIDFA : function (){
                     			 return B.execSync(_BARCODE, "getIDFA", []);
                    	  }
                    };
                    window.plus.FMTools = FMTools;
   		
   	
   	
   	
        var webview = plus.webview.currentWebview();
        plus.key.addEventListener('backbutton', function() {
            webview.canBack(function(e) {
                if(e.canBack) {
                    webview.back(); 
                             
                  } else {
//                   webview.close(); //hide,quit
//                   plus.runtime.quit();
//                 
                        //首页返回键处理
                        //处理逻辑：1秒内，连续两次按返回键，则退出应用；
                        var first = null;
                        plus.key.addEventListener('backbutton', function() {
                            //首次按键，提示‘再按一次退出应用’
                            if (!first) {
                               let instance = Toast('再按一次返回键退出米多星');  
																setTimeout(() => {
																  instance.close();
																}, 1000);
                                first = new Date().getTime();
                                setTimeout(function(){
                                	first = null;
                                },1000);
                            }else{
                            	 if(new Date().getTime() - first <1000){
                            	 		plus.runtime.quit();
                            	 }
                            }
                          },false)
                       }
                })
            })
        })
                           


