<template>
	<div class="goldExchange">
		<div class="goldExchange_header">
			<div class="back" @click="back"></div>
			<div class="center">兑换</div>
			<div class="block" @click="to_record">兑换记录</div>
		</div>
		<div class="banner">
			<div>我的金币</div>
			<div>{{corn}}个</div>
			<div>今日汇率{{huilv}}</div>
		</div>
		<div class="exchangeTab">
			<div class="exchangeTab1" v-if="enough">
				<div class="exchangeButton" @click="showBox">现金兑换</div>
				<div class="exchangeText">XXX金币起兑换</div>
			</div>
			<div class="exchangeTab2" v-if="!enough">
				<div class="exchangeButton" @click="to_team">获取金币</div>
				<div class="exchangeText">XXX金币起兑换，<span>金币不足</span></div>
			</div>
		</div>
		<div class="Changelist">
			<div class="ChangeHead">兑换榜单</div>
			<div class="Head2">
				<span>排名</span>
				<span>头像</span>
				<span>昵称</span>
				<span>个数</span>
			</div>
			<div class="paihangContent">
				<div class="cup">
					<div class="one"></div>
					<div class="two"></div>
					<div class="three"></div>
				</div>
	            <div class="userTop" v-for="data in datas" :key="data.id">
						<div class="userHeadPic"><img :src="'http://'+data.userHeadPic" /></div>
						<div class="nicName">{{data.userName}}</div>
						<div class="amount">{{data.amount}}</div>
				</div>
			</div>	
		</div>
		<div class="zheZhao" v-show="changeBox">
			<div class="tanKuang">
				<div class="top">兑换<span @click="cha"></span></div>
				<div class="center">
					<div class="left">
						<div>输入金币的数量</div>
						<div>(100倍数)</div>
					</div>
					<div class="right">
						 <div class="rightTop">
						 	<input type="text" v-model="exchangeInput"/>
						 	<span>↑</span>
						 </div>
						 <div class="rightButtom">
						 	剩余{{left}}个
						 </div>
					</div>
				</div>
				<div class="buttom1" v-if="shuru">确定</div>
				<div class="buttom2" v-if="!shuru" @click="changeSuccess">确定</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				//我的金币
				corn:11111,
				//huilv
				huilv:"1%",
				enough:true,
				//是否输入
				shuru:true,
				//弹窗显示隐藏
				changeBox:false,
				datas:[],
				//输入兑换金额
				exchangeInput:""
			}
		},
		methods:{
			back(){
				this.$router.push('/my')
			},
			to_team(){
				this.$router.push('/GoTeam')
			},
			//点击显示对焊弹窗
			showBox(){
				this.changeBox = true
			},
			//点击关闭弹窗
			cha(){
				this.changeBox = false
			},
			//点击跳转兑换成功
			changeSuccess(){
				console.log("兑换成功")
			},
			to_record(){
				this.$router.push('/my/GoldExchange/GoldExchangeRecord')
			}
		},
		updated(){
			if(this.exchangeInput!==""&&this.exchangeInput % 100 == 0&&this.left>=0){
				this.shuru = false
			}else{
				this.shuru = true
				
			}
		},
		 mounted(){
            this.$http({
					method:'POST',
					url:this.$getTop,
			}).then((res)=>{
				this.datas = res.data.data
		
			})
        },
        computed:{
        	left(){
			return this.corn - this.exchangeInput      	     		
        	}

        }
	}
</script>

<style scoped lang="scss">
.goldExchange{
	width: 100%;
	height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	.goldExchange_header{
						
			  	width: 100%;
				height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 3.81rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 3.81rem;
					  	height: 100%;
					  	text-align: center;
					  	line-height: 2.81rem;
					  	font-size: 0.93rem;
					  	color: #714739;
					  }
 	 
		
	
	}
	.banner{
		width: 100%;
		height: 7.53rem;
		background: url(../../../assets/changebanner.png) no-repeat center center;
		background-size: 100% 100%;
		display: flex;
		flex-direction: column;
		div{
			flex: 1;
			text-align: center;
			width: 100%;
			line-height: 2.51rem;
			color: #ffffff;
		}
		div:nth-child(1){
			font-size: 0.87rem;
		}
		div:nth-child(2){
			font-size: 1.68rem;
		}
		div:nth-child(3){
			font-size: 1.12rem;
		}
	}
	.exchangeTab{
		width: 100%;
		height: 8rem;
		.exchangeTab1{
		width: 100%;
		height: 8rem;
		background: #fec901;
		padding-top: 2.28rem;
		.exchangeButton{
			width: 9.9rem;
			height: 2.68rem;
			text-align: center;
			line-height: 2.68rem;
			color: #ffffff;
			background: #fe2b00;
			border-radius: 5px;
			margin: 0 auto;
			font-size: 1.18rem;
		}
		.exchangeText{
			width: 100%;
			height: 2.96rem;
			text-align: center;
			line-height: 2.96rem;
			font-size: 0.71rem;
			color: #0a0a0c;
		}
	}
	.exchangeTab2{
		width: 100%;
		height: 8rem;
		background: #fec901;
		padding-top: 2.28rem;
		.exchangeButton{
			width: 9.9rem;
			height: 2.68rem;
			text-align: center;
			line-height: 2.68rem;
			color: #f94b4b;
			border: 1px solid #f94b4b;
			background: #ffffff;
			border-radius: 5px;
			margin: 0 auto;
			font-size: 1.18rem;
		}
		.exchangeText{
			width: 100%;
			height: 2.96rem;
			text-align: center;
			line-height: 2.96rem;
			font-size: 0.71rem;
			color: #0a0a0c;
			span{
				color: #ff1900;
			}
		}
	}
	}
	.Changelist{
		overflow: hidden;
		flex: 1;
		width: 100%;
		overflow-y: scroll;
		.ChangeHead{
			width: 100%;
			height: 1.25rem;
			line-height: 1.25rem;
			padding-left: 0.62rem;
			background: #8237e9;
			color: #ffffff;
			font-size: 0.71rem;
		}
		.Head2{			
            height: .9375rem;
			line-height: .9375rem;
			font-size: .6875rem;
			color: #4d4d4d;
			margin-top: .1875rem;
			display: flex;
			span{
				display: block;
				flex: 1;
				height: 100%;
				text-align: center;
				line-height: .937rem;
			}
        
		}
		.paihangContent{
            width: 100%;
            padding-left: 3.31rem;
            padding-right: .5625rem;
            position: relative;
            .cup{
                	width: 2.13125rem;
                    position: absolute;
                    top: 0;
                    left: 1.48rem;
                    flex-direction: column;
                    justify-content: space-between;
                    display:flex;
                    .one{
                        width: 2.71rem;
                        height: 2.0625rem;
                        background: url('../../../assets/topOne.png') no-repeat center center;
                        background-size: 1.31rem 1.5rem;
                    }
                    .two{
                        width: 2.71rem;
                        height: 2.0625rem;
                        background: url('../../../assets/topTwo.png') no-repeat center center;
                        background-size: 1.31rem 1.5rem;   		
                    }
                    .three{
                        width: 2.71rem;
                        height: 2.0625rem;
                        background: url(../../../assets/topThree.png) no-repeat center center;
                        background-size: 1.31rem 1.5rem;   		
                    }
            }

        	.userTop{
   		width: 100%;
   		height: 1.71rem;
   		display: flex;
   		margin-bottom:0.53rem;
   		padding-left: 4.56rem;
   		.userHeadPic{
   			width: 1.75rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			
   			img{
   				width: 1.25rem;
   				height: 1.25rem;
   			}
   		}
   		.nicName{
   			width: 4.28rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			font-size: 0.59rem;
   			margin-left: 2.56rem;
   		}

   		.amount{
   			width: 2.06rem;
   			height: 100%;
   			text-align: center;
   			line-height: 1.71rem;
   			font-size: 0.59rem;
   			margin-left: 2.94rem;
   		}
   	}

        }
	}
	.zheZhao{
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background: rgba(0,0,0,.5);
		.tanKuang{
			width: 15.43rem;
			height: 8.5rem;
			background: #ffffff;
			margin: 8.62rem auto;
			border-radius: 10px;
			overflow:hidden;
			.top{
				width: 100%;
				height: 2.25rem;
				text-align: center;
				line-height: 2.25rem;
				font-size: 0.71rem;
				color:#5f5f5f ;
				position: relative;
				span{
					display: block;
					position: absolute;
					width: 1.25rem;
					height: 1.25rem;
					background: url(../../../assets/heicha.png) no-repeat center center;
					background-size: 0.93rem 0.93rem;
					top: 0.46rem;
					right: 0.62rem;
				}
			}
			.center{
				width: 100%;
				height: 2.37rem;
				margin-top: 0.59rem;
				display: flex;
				.left{
					width: 7.03rem;
					height:100%;
					div{
						color:#343434 ;
						text-align: center;						
					}
					div:nth-child(1){
						width: 100%;
						 height: 1.31rem;
						 line-height: 1.31rem;
						font-size: 0.71rem;
					}
					div:nth-child(2){
						width: 100%;
						height: 1.09rem;
					}
				}
				.right{
					flex: 1;
					height: 100%;
					.rightTop{
						width: 100%;
						height: 1.31rem;
						display: flex;
						input{
							display: block;
							width: 7.12rem;
							height: 100%;
						}
						span{
							flex: 1;
							display: block;
							height: 100%;
							text-align: center;
							line-height: 1.31rem;
							
						}
					}
					.rightButtom{
						width: 100%;
						height: 1.09rem;
						font-size: 0.62rem;
						line-height: 1.09rem;
					}
				}
			}
			.buttom1{
				width: 100%;
				height: 1.87rem;
				margin-top: 1.53rem;
				background: #c2c2c2;
				text-align: center;
				line-height: 1.87rem;
				font-size: 0.84rem;
				color: #ffffff;
			}
			.buttom2{
				width: 100%;
				height: 1.87rem;
				margin-top: 1.53rem;
				background: #8237e9;
				text-align: center;
				line-height: 1.87rem;
				font-size: 0.84rem;
				color: #ffffff;
			}
		}
	}
}
</style>


