<template>
    <div class="myposter">
        <div class="poster_header">
			<div class="back" @click="back"></div>
			<div class="center">我的海报</div>
			<div class="block" @click="transpond"></div>
		</div>
        <div class="poster_top">
            <div class="info">
                <div class="tou"></div>
                 <p>{{this.userName}}</p>
                    <p>ID {{this.userId}}</p>
            </div>
            <div class="content">
                <p>1.注册即得现金红包</p>
                <p>2.随时登陆，赚点零花钱</p>
                <p>3.邀请好友即得现金红包</p>
                <p>4.好友完成任务即得永久奖励</p>
                <span>动动你的手指来领取吧</span>
            </div>
        </div>
        <div class="poster_btm">
            <div class="two_code"><img src="../../assets/erweima.png" alt=""></div>
            <p>邀请码{{this.inviteCode}}</p>
            <div class="saveImg" @click="saveImg">保存图片</div>
        </div>
        <div class="mubu" v-if="isShow">
            <div class="share_way">
                <div class="share_top">
                    <img src="../../assets/sureCha.png" alt="" @click="close">
                    <span>选择分享方式</span>
                </div>
                <div class="share_btm">
                    <div class="wx" @click="wxShare">
                        <img src="../../assets/weixin.png" alt="">
                        <p>微信好友</p>
                    </div>
                    <div class="qq" @click="qqShare">
                        <img src="../../assets/qq.png" alt="">
                        <p>QQ好友</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
    export default {
        name:'myposter',
        data(){
            return{
                userId:'',
                userName:'',
                inviteCode:1234567,
                isShow:false
            }
        },
        methods:{
            back(){
                this.$router.go(-1)
            },
            //转发按钮
            transpond(){
                this.isShow = true;
            },
            //关闭弹窗
            close(){
                this.isShow = false;
            },
            //微信分享
            wxShare(){

            },
            //qq分享
            qqShare(){

            },
            //保存图片
            saveImg(){
                savePicture()
                
                // 扩展API加载完毕后调用onPlusReady回调函数 


                document.addEventListener( "plusready", onPlusReady, false );
                var r = null; 
                // 扩展API加载完毕，现在可以正常调用扩展API 
                function onPlusReady() {
                }
                
                // 保存图片到相册中 
                function savePicture() {
                    plus.gallery.save( "../../assets/erweima.png'", function () {
                        let instance = Toast({
                                message: '保存成功',
                                position: 'bottom',
                                duration: 2000
                                });
                        setTimeout(() => {
                        instance.close();
                        }, 2000);
                    } );
                }

                
               
            }
        },
        beforeMount(){
            this.userId = JSON.parse(localStorage.getItem('userId')).id;
            this.userName = JSON.parse(localStorage.getItem('userId')).phone;
        }
    }
</script>

<style scoped='' lang='scss'>
    .myposter{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        background: #f1f1f1;
		overflow: hidden;
        .poster_header{
            width: 100%;
				height: 2.8125rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
                        background: url('../../assets/zhuan.png') no-repeat center center;
                        background-size: 1.5rem 1.5rem;
					  }
              }
        
        .poster_top{
            height: 13.1875rem;
            width: 100%;
            background: #f94b4c;
            display: flex;
            overflow: hidden;
            text-align: center;
            .info{
                width: 5.34375rem;
                height: 100%;
                margin-top: 2.0625rem;
                margin-left: 1.8125rem;
                .tou{
                    display: inline-block;
                    height: 4.21875rem;
                    width: 4.21875rem;
                    border-radius: 50%;
                    background: url('../../assets/head2.png') no-repeat center center;
                    background-size: 4.21875rem 4.21875rem;
                }
                P{
                    color: #f0f2f1;font-size: .84375rem;margin-top: .84375rem;
                }
            }
            .content{
                margin-top: 3.09375rem;
                margin-left: 4.3125rem;
                text-align: left;
                p{font-size: .875rem;color: #ffffff;line-height: 1.3125rem;}
                span{font-size: .875rem;color: #f9e34f;line-height: 1.3125rem;}
            }
        }
        .poster_btm{
            width: 100%;
           flex: 1;
           text-align: center;
           .two_code{
               margin-top: 2.125rem;
               height: 8.34375rem;
               width: 8.46875rem;
               display: inline-block;
            img{width: 8.46875rem;height: 8.34375rem;}
           }
           p{margin-top: 1.1875rem;font-size: 1rem;color: #f84a4b;}
           .saveImg{
                    display: inline-block;
                    margin-top: 1.25rem;
                    width: 13.78125rem;
                    height: 2.03125rem;
                    line-height: 2.03125rem;
                    text-align: center;
                    color: #ffffff;
                    font-size: 1.09375rem;
                    background: #f94b4c;
                    border-radius: .375rem;
                    }
        }
        .mubu{
            position: fixed;
            height: 100%;
            width: 100%;
            top: 0;
            background: rgba(135,131,130,.7);
            .share_way{
                height: 6.25rem;
                width: 100%;
                position: fixed;
                bottom: 0;
                background: #cacaca;
                .share_top{
                    height: 1.03125rem;line-height: 1.03125rem;
                      img{height: 1.03125rem;width: 1.03125rem;margin-left: .625rem;}
                    span{font-size: .875rem;color: #e94a47;margin-top: .3125rem;display: inline-block;margin-left: 7.5rem;}
                }
              .share_btm{
                  height: 3.84375rem;
                  width: 100%;
                  margin-top: 1.125rem;
                  display: flex;
                  justify-content: space-around;
                  div{
                      height: 3.84375rem;width: 3.25rem;
                      text-align: center;
                      p{font-size: .8125rem;color: #2a2a2c;margin-top: .625rem;}
                      }
                  .wx{img{height: 1.90625rem;width: 2.34375rem;}}
                  .qq{img{width: 1.8125rem;height: 1.8125rem;}}
              }
            }
        }
    }
</style>


